export function gerarStringAleatoria(tamanho = 6) {
	// removido letras o O l i I e números 0 e 1 pra evitar confusão na hora de confirmar o captcha
	// let caracteres = "23456789abcdefghjkmnpqrstuvwxyzABCDEFGHJKLMNPQRSTUVWXYZ!#$%*";

	// removido caracteres pra deixar o catpcha mais fácil para digitar
	let caracteres = "23456789ABCDEFGHJKLMNPQRSTUVWXYZ";
	let sa = '';

	for (let i = 0; i < tamanho; i++) {
		sa += caracteres.charAt(Math.floor(Math.random() * caracteres.length));
	}
	
	// retorna o string aleatória gerada
	return sa;
}

/**
 * Gera um número entre um valor minimo e maximo informados
 * @param number min 
 * @param number max 
 * @returns  number
 */
export const gerarNrEntre = (min, max) => Math.floor(Math.random() * (max - min + 1) + min);

/**
 * @param {*} start 
 * @param {*} stop 
 * @param {*} step 
 * @see https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Array/from#sequence_generator_range 
 * @returns 
 */
export const range = (start, stop, step) => Array.from({ length: (stop - start) / step + 1}, (_, i) => start + (i * step));

 /**
  * Gera/retorna uma string invertida
  * @param string str 
  * @returns 
  */
 export const revstr = (str) => str.split("").reverse().join("");