<script>
	import { onMount } from 'svelte';
	import { push } from 'svelte-spa-router';
	import { sHorasVidaUtilToken, sTema, sTituloPaginaAtual } from '../stores';
	import { pegarChavePeloValor } from './../utils/Objetos';
	import { parseJWT } from "../utils/JWT";
    import { gerarQueryString, montarURN } from '../utils/QueryString';
    import TipoAcesso from '../constantes/TipoAcesso';

	let tema = "dia";
	let anoBase = "";
	let dadosUsuarioP;
	let menuAberto = false;
	let menuElemento;
	
	onMount(async () => {
		let token = parseJWT(sessionStorage.getItem('ECONRIO_TOKEN')); 

		if(!localStorage.getItem('ECONRIO_CONFIG_APP_TEMA'))
			localStorage.setItem('ECONRIO_CONFIG_APP_TEMA', tema);

		tema = localStorage.getItem('ECONRIO_CONFIG_APP_TEMA');
		anoBase = localStorage.getItem('ECONRIO_CONFIG_APP_ANOBASE');

		sTema.update(novoTema => tema);
		
		let dataExpiracaoToken;

		if (token) {
			// * 1000 pois claim exp é o timestamp em milissegundos
			dataExpiracaoToken = new Date(token.exp * 1000);
			sHorasVidaUtilToken.update(() => dataExpiracaoToken.getHours() - new Date().getHours());
			dadosUsuarioP.innerHTML = `<span id="ledVidaToken" title="Saúde do token de acesso">&#11044;</span> <strong>${token.cpf}</strong> / <span title="Ano Base" class="has-text-link-dark">AB: ${anoBase}</span> / Acesso: ${pegarChavePeloValor(TipoAcesso, token.tipo_em_uso)}`;
		}

		menuElemento = document.getElementById("menu");

		let elementoLed = document.getElementById('ledVidaToken');

		if ($sHorasVidaUtilToken < 2 || dataExpiracaoToken.getDay() != new Date().getDay())
			elementoLed.style.color = "#F00";
		else 
			elementoLed.style.color = "#0F0";

		elementoLed.animate([
			{ opacity: 0.3 },
			{ opacity: 1 }, 
			{ opacity: 0.3 }
		], {
			duration:1000,
			iterations:Infinity
		});
	});

	const trocarTema = () => {
		if (localStorage.getItem('ECONRIO_CONFIG_APP_TEMA') == "dia") tema = "noite";
		else tema = "dia"
		
		sTema.update(novoTema => tema);
		localStorage.setItem('ECONRIO_CONFIG_APP_TEMA', tema);
	}

	const sair = () => {
		push(montarURN('limpeza-aplicacao', gerarQueryString({ mensagem: "Saindo, aguarde ..." })));
	}

	const abrirOuFecharMenu = () => {
		if (menuAberto) {
			menuAberto = false;
			menuElemento.classList.remove('exibe-botoes');
			menuElemento.classList.add('esconde-botoes');
			
			document.querySelectorAll('div.seta-abrir').item(0).classList.remove('is-hidden');
			document.querySelectorAll('div.seta-fechar').item(0).classList.add('is-hidden');
		} else {
			menuAberto = true;
			menuElemento.classList.remove('esconde-botoes');
			menuElemento.classList.add('exibe-botoes');

			document.querySelectorAll('div.seta-abrir').item(0).classList.add('is-hidden');
			document.querySelectorAll('div.seta-fechar').item(0).classList.remove('is-hidden');
		}
	}
</script>

<style>
	.barra-esquerda-1 {
		position: fixed;
		top: 10px;
		left: -10px;
		z-index: 10;
		background-color: #f5f5f5;
		border-radius: 8px;
		padding-left: 10px;
		border: 1px solid #ddd;
	}

	.barra-esquerda-2 {
		position: fixed;
		top: 44px;
		left: -10px;
		z-index: 10;
		background-color: #000;
		color: #fff !important;
		font-weight: bolder;
		border-radius: 8px;
		padding-left: 12px;
	}

	.menu-container {
		position: fixed;
		top: -10px;
		z-index: 10;
		cursor: pointer;
		right: 20px;
		
		border-radius: 8px;

		transition: all 0.2s cubic-bezier(.25, .8, .25, 1);
	}

	.esconde-botoes {
		top: -320px;
	}

	.exibe-botoes {
		top: -10px;
	}	

	.menu-item {
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
		height: 100px;
		width: 120px;
	}

	.menu-item-icone {
		height: 50px;		
	}

	.menu-item-texto {
		height: 50px;
	}

	.seta-fix {
		position:relative; 
		top: 16px
	}

	.seta-fechar {
		width: 0; 
		height: 0; 
		border-left: 10px solid transparent;
		border-right: 10px solid transparent;

		border-bottom: 10px solid #444;
	}

	.seta-abrir {
		width: 0; 
		height: 0; 
		border-left: 10px solid transparent;
		border-right: 10px solid transparent;

		border-top: 10px solid #444;
	}
</style>

<div class="barra-esquerda-1">
	<p class="has-text-primary is-size-7 px-3 py-1" bind:this={dadosUsuarioP}></p>
</div>

<div class="barra-esquerda-2">
	<p class="is-size-6 px-3 py-1">{ $sTituloPaginaAtual }</p>
</div>

<div id="menu" class="menu-container esconde-botoes has-background-black">
	
	<div class="pt-4"></div>

	<a href="#/painel" class="menu-item">
		<div class="menu-item-icone">
			<span title="Voltar ao painel principal">
				<figure class="image is-24x24 pt-3">
					<img src="painel.png" alt="ir para painel">
				</figure>
			</span>
		</div>
		<div class="menu-item-texto">
			<strong class="has-text-grey-lighter">PAINEL</strong>
		</div>
	</a>
	<span class="menu-item" on:click|preventDefault={trocarTema}>
		<div class="menu-item-icone">
			<span title="Trocar tema - claro / escuro">
				{#if tema == "dia"}
				<figure class="image is-24x24 pt-3">
					<img src="sol.png" alt="trocar tema - dia">
				</figure>
				{:else}
				<figure class="image is-24x24 pt-3">
					<img src="lua.png" alt="trocar tema - noite">
				</figure>
				{/if}
			</span>
		</div>
		<div class="menu-item-texto">
			<strong class="has-text-grey-lighter">TEMA</strong>
		</div>
	</span>
	<span class="menu-item" on:click|preventDefault={sair}>
		<div class="menu-item-icone">
			<span title="Sair (destruir token)">
				<figure class="image is-24x24 pt-3">
					<img src="cadeado-fechado.png" alt="sair">
				</figure>
			</span>
		</div>
		<div class="menu-item-texto">
			<strong class="has-text-grey-lighter">SAIR</strong>
		</div>
	</span>
	
	<div class="menu-item" on:click={abrirOuFecharMenu}>
		<strong title="Clique ou toque para abrir/fechar" class="has-text-grey">
			MENU
		</strong>
		<div class="seta-fix seta-abrir" title="Abrir menu"></div>
		<div class="seta-fix seta-fechar is-hidden" title="Fechar menu"></div>
	</div>
</div>