<script>
	import { createEventDispatcher, onMount } from 'svelte';
	import { querystring, replace } from 'svelte-spa-router';
	import { sCaptcha } from '../../stores';
	import TBox from '../templates/TBox.svelte';
	import Captcha from '../../componentes/Captcha.svelte';
    import { gerarQueryString, montarURN, recuperarValorParametroQS, verificarTimestampQueryString } from '../../utils/QueryString';
    import TipoEvento from '../../constantes/TipoEvento';
    import { API, cabecalhoRequisicao, enviarDadosFormulario } from '../../utils/Requisicao';
    import { gerarStringAleatoria } from '../../utils/Geradores';
    import TextoFeedback from '../../constantes/TextoFeedback';
	import TipoBloco from '../../constantes/TipoBloco';
    import { parseJWT } from '../../utils/JWT';
    import TipoAcesso from '../../constantes/TipoAcesso';

	const dispatch = createEventDispatcher();
	
	// variáveis para trabalhar com os dados do formulário
	let conteudo;
	let idPivot, idBloco, codigo, tituloProjeto;
	let tituloBloco = "";
	let tipoBloco = "";
	let bloco = {};
	let token;
	
	onMount(async () => {
		dispatch('routeEvent', { 
			tipo: TipoEvento.ESTA_CARREGANDO,
			valor: true,
			mensagem: 'Preparando formulário ...'
		});
		
		let parametrosURL = new URLSearchParams($querystring);
		verificarTimestampQueryString(parametrosURL.get('q'));
		
		tituloProjeto = recuperarValorParametroQS(parametrosURL.get('q'), 'tituloProjeto');
		codigo = recuperarValorParametroQS(parametrosURL.get('q'), 'codigoProjeto');

		token = parseJWT(sessionStorage.getItem('ECONRIO_TOKEN')); 
		
		if (token.tipo_em_uso != TipoAcesso.DOCENTE)
			throw new Error('Operação requer tipo de acesso diferente do seu. Caso seu usuário possua mais de um tipo de acesso, sair da aplicação e acessar com o tipo de usuário adequado para executar a operação.');

		bloco = JSON.parse(localStorage.getItem('TEMP_BLOCO_CONTEUDO_ATUALIZACAO'));
		
		tipoBloco = bloco?.tipo;
		tituloBloco = bloco?.titulo;
		conteudo = bloco?.conteudo;
		idBloco = bloco?.id;
		idPivot = bloco?.id_pivot;

		dispatch('routeEvent', { 
			tipo: TipoEvento.ESTA_CARREGANDO,
			valor: false
		});
	});

	async function submeterFormulario(event) {
		enviarDadosFormulario('PATCH', dispatch, replace, `projetos/blocos/conteudo/${idPivot}`, 
			JSON.stringify({
				'_method': 'PATCH',
				idBloco: idBloco,
				codigo: codigo,
				conteudo: conteudo,
			}), 
			'Projeto atualizado com sucesso!', 
			montarURN('projeto/visualizacao', gerarQueryString({ codigoProjeto: codigo})), 
			'#/painel');
	}
</script>

<TBox 
	titulo="Conteúdo do Projeto" 
	subtitulo="Atualizando bloco de conteúdo" 
	largura="10" 
	deslocamento="1" 
	correcaoY="-13px"
	icone="projeto"
	exibirBox>
	
	<form method="post" on:submit|preventDefault={submeterFormulario}>
		<input type="hidden" name="_method" value="PATCH">

		<div class="columns">
			<div class="column">
				<p>{ tituloBloco }:</p>
				<br>

				<div class="field">
				{#if tipoBloco == TipoBloco.LOGICO}
				<div class="select is-primary is-large is-fullwidth">
					<select bind:value={conteudo} required>
						<option value="">Escolha uma opção</option>
						<option value="1">SIM</option>
						<option value="0">NÃO</option>
					</select>
				</div>
				{:else if tipoBloco == TipoBloco.NUMERICO}
				<input 
					bind:value={conteudo}
					type="tel" 
					pattern="[0-9]*" 
					maxlength="10" 
					placeholder="Inserir valor numérico ..." 
					class="input is-large is-primary" required>
				{:else}
				<textarea 
					bind:value={conteudo}
					class="textarea is-primary is-size-5" 
					placeholder="Escreva aqui o conteúdo..."
					name="conteudo" required></textarea>
				{/if}
				</div>
			</div>
		</div>

		<div class="columns">
			<div class="column">
				<p class="control">
					<a href="{montarURN('projeto/atualizacao', gerarQueryString({ 
						codigoProjeto: codigo 
					}))}" class="button is-black is-medium is-outlined is-fullwidth">Voltar</a>
				</p>
			</div>
			<div class="column is-8">
				<p class="control">
					<button 
						type="submit" 
						class="button is-primary is-medium is-fullwidth">
						Atualizar
					</button>
				</p>
			</div>
		</div>
	</form>
</TBox>