<script> 
	import { push, replace, querystring } from 'svelte-spa-router';
	import { createEventDispatcher, onMount } from 'svelte';
	import TBoxInterno from '../templates/TBoxInterno.svelte';
	import BarraOperacoesRegistro from '../../componentes/BarraOperacoesRegistro.svelte';
	import { sTituloPaginaAtual } from '../../stores';
	import BarraOperacoesRegistroVertical from '../../componentes/BarraOperacoesRegistroVertical.svelte';
    import TipoEvento from '../../constantes/TipoEvento';
    import { API, cabecalhoRequisicao } from '../../utils/Requisicao';
    import { gerarQueryString, montarURN, recuperarValorParametroQS, verificarTimestampQueryString } from '../../utils/QueryString';
    import { parseJWT } from '../../utils/JWT';
    import TipoAcesso from '../../constantes/TipoAcesso';
	import { formatarCPF } from '../../utils/CPF.js';
    import StatusHttp from '../../constantes/StatusHttp';
    import TipoAnexavel from '../../constantes/TipoAnexavel';

	const dispatch = createEventDispatcher();
	let usuario = {};
	let cpfFormatado;
	let token = {};
	let anexos = [];
	let dataExpiracaoToken = "";

	let menuOpcoes = [{
		titulo: 'Painel',
		cor: 'is-black is-outlined',
		callback: () => push('#/painel')
	}];

	onMount(async () => {
		dispatch('routeEvent', {
			tipo: TipoEvento.ESTA_CARREGANDO,
			valor: true,
			mensagem: 'Carregando usuário ...',
		});
		
		let parametrosURL = new URLSearchParams($querystring);
		verificarTimestampQueryString(parametrosURL.get('q'));
		let cpf = recuperarValorParametroQS(parametrosURL.get('q'), 'cpf');

		$sTituloPaginaAtual = `Visualização de Usuário (CPF: ${formatarCPF(cpf)})`;
		
		token = parseJWT(sessionStorage.getItem('ECONRIO_TOKEN'));

		const resposta = await fetch(API(`usuarios/${cpf}`), {
			method: 'GET',
			headers: cabecalhoRequisicao()
		}).catch(erro => { throw new Error(`[${ erro.message }] - ${ TextoFeedback.ERRO_REQUISICAO }`) });
		
		const json = await resposta.json();
		// const removerAnexo = async (idAnexo) => removerAnexoGenerico(idAnexo, dispatch, push, '#/painel/usuarios', 'usuarios/anexos/remover');
		dataExpiracaoToken = new Date(parseInt(token.exp) * 1000).toLocaleString();

		if (resposta.status == StatusHttp.OK) {
			usuario = json.data;
			cpfFormatado = formatarCPF(usuario.cpf);
			anexos = [... usuario.anexos];
		} else throw new Error(json.mensagem ?? 'Não foi possível processar esta requisição.');

		// permitir apenas atualizar o próprio cadastro (admin pode atualizar cadastro de outros usuários)
		if (token?.cpf == usuario?.cpf) {
			menuOpcoes.push({
				titulo: 'Adicionar anexo',
				callback: () => push(montarURN('formularios/anexo/cadastro', gerarQueryString({ 
					tipoAnexavel: TipoAnexavel.USUARIO,
					identificadorAnexavel: usuario.cpf,
				})))
			});
		}

		menuOpcoes = [... menuOpcoes];

		dispatch('routeEvent', { 
			tipo: TipoEvento.ESTA_CARREGANDO,
			valor: false
		});
	});
</script>

<TBoxInterno 
	titulo={usuario.nome} 
	subtitulo="..."
	largura="10" 
	icone="usuario"
	deslocamento="1">
	
	<div class="columns">
		<div class="column is-8">
			<p class="is-size-4 mb-3">Dados:</p>
			<div class="permitir-selecao">
				<p>CPF: <strong>{cpfFormatado}</strong></p>
				<p>E-mail: <strong>{usuario.email}</strong></p>
			</div>
		
			{#if usuario?.cpf == token?.cpf}
			<br>
			<div class="notification is-danger is-light">
				<strong>Expiração do seu token de acesso: { dataExpiracaoToken }</strong>
				<br>
				<small>Para renovar seu token de acesso, acesse 'Painel > Configurações'</small>
			</div>
			{/if}
		</div>

		<div class="column" style="border-left:1px dashed #aaa">
			<p class="is-size-4 mb-3">Operações:</p>
			<BarraOperacoesRegistroVertical opcoes={menuOpcoes} />
		</div>
	</div>
	
	{#if anexos?.length > 0}
	<div class="columns">
		<div class="column">
			<p class="is-size-4 mb-4">Anexos</p>

			<table class="table is-fullwidth is-hoverable permitir-selecao">
				<thead>
					<tr>
						<th>Tipo</th>
						<th>Valor</th>
					</tr>
				</thead>
				<tbody>
					{#each anexos as anexo}
					<tr>
						<td>{ anexo.tipo }</td>
						<td>{ anexo.valor }</td>
					</tr>
					{/each}
				</tbody>
			</table>
		</div>
	</div>
	{/if}
</TBoxInterno>