<script>
	import { replace, querystring, push } from 'svelte-spa-router';
	import { createEventDispatcher, onMount } from 'svelte';
    import { gerarQueryString, montarURN, recuperarValorParametroQS, verificarTimestampQueryString } from '../../utils/QueryString';
    import { parseJWT } from '../../utils/JWT';
    import { API, cabecalhoRequisicao, enviarDadosFormulario } from '../../utils/Requisicao';
    import TipoEvento from '../../constantes/TipoEvento';
    import TextoFeedback from '../../constantes/TextoFeedback';
    import StatusHttp from '../../constantes/StatusHttp';
    import TipoAcesso from '../../constantes/TipoAcesso';
    import TBox from '../templates/TBox.svelte';
    import { verificarSessionStorage } from '../../utils/Verificadores';
    import { formatarCodigo } from '../../utils/Projeto';

	const dispatch = createEventDispatcher();

	let projeto = {};
	let titulo = ""; 
	let instituicao, alocacao, curso;
	let conteudo = [];

	let instituicoes = [];
	let areasConhecimento = [];
	let cursos = [];
	let envolvePesquisaAnimaisOuHumanos;
	let numeroCAAE;
	let divNumeroCAAE;
	let informacosAPICache = null;
	
	let totalCaracteresTitulo = 0;
	$: totalCaracteresTitulo=titulo.length;
	let token;
	let codigo;

	onMount(async () => {
		dispatch('routeEvent', {
			tipo: TipoEvento.ESTA_CARREGANDO,
			valor: true,
			mensagem: 'Carregando projeto ...',
		});
		
		if (!verificarSessionStorage('ECONRIO_TOKEN'))
			replace("/painel");

		informacosAPICache = JSON.parse(localStorage.getItem('ECONRIO_API_INFO_CACHE'));
		instituicoes = informacosAPICache.instituicoes;
		areasConhecimento = informacosAPICache.areas_conhecimento;
		cursos = informacosAPICache.cursos;
		divNumeroCAAE = document.getElementById('divNumeroCAAE');
		
		let parametrosURL = new URLSearchParams($querystring);
		verificarTimestampQueryString(parametrosURL.get('q'));
		codigo = recuperarValorParametroQS(parametrosURL.get('q'), 'codigoProjeto');
		
		token = parseJWT(sessionStorage.getItem('ECONRIO_TOKEN')); 

		if (token.tipo_em_uso != TipoAcesso.DOCENTE)
			throw new Error('Operação requer tipo de acesso diferente do seu. Caso seu usuário possua mais de um tipo de acesso, sair da aplicação e acessar com o tipo de usuário adequado para executar a operação.');

		const resposta = await fetch(API(`projetos/${codigo}`), {
			method: 'GET',
			headers: cabecalhoRequisicao()
		}).catch(erros => {
			console.log(erros);
			
			dispatch('routeEvent', {
				tipo: TipoEvento.ABRIR_MODAL,
				titulo: 'Erro',
				conteudo: TextoFeedback.ERRO_REQUISICAO
			});

			replace("#/painel");
		});

		const { data } = await resposta.json();

		if (resposta.status == StatusHttp.OK) {
			projeto = data;
			conteudo = projeto.conteudo;

			titulo = projeto?.titulo;
			instituicao = projeto?.instituicao_raw;
			alocacao = projeto?.alocacao_raw;
			curso = projeto?.curso_raw;
			envolvePesquisaAnimaisOuHumanos = projeto?.envolve_animais_humanos_raw == true ? 1 : 0;
			numeroCAAE = projeto?.numero_caae;

			if (envolvePesquisaAnimaisOuHumanos == 1)
				divNumeroCAAE.classList.remove('is-hidden');
		} else {
			dispatch('routeEvent', { 
				tipo: TipoEvento.ESTA_CARREGANDO,
				valor: false
			});
			
			replace('#/projeto-inexistente');
			return;
		}

		dispatch('routeEvent', { 
			tipo: TipoEvento.ESTA_CARREGANDO,
			valor: false
		});
	});

	async function submeterFormulario(event) {
		enviarDadosFormulario('POST', dispatch, replace, `projetos/${codigo}`, 
			JSON.stringify({
				'_method': 'PATCH',
				titulo: titulo, 
				instituicao: instituicao,
				alocacao: alocacao,
				curso: curso,
				envolve_animais_humanos: envolvePesquisaAnimaisOuHumanos,
				numero_caae: envolvePesquisaAnimaisOuHumanos == 1 ? numeroCAAE : null
			}), 
			'Projeto atualizado com sucesso!', 
			montarURN('projeto/visualizacao', gerarQueryString({ codigoProjeto: codigo})), 
			'#/painel');
	}

	const exibirEsconderNumeroCAEE = (evento) => {
		let escolha = parseInt(evento.currentTarget.value);

		if (escolha == 1)
			divNumeroCAAE.classList.remove('is-hidden');
		else
			divNumeroCAAE.classList.add('is-hidden');
	}

	const prepararBlocoPraAtualizacao = (bloco) => {
		localStorage.setItem('TEMP_BLOCO_CONTEUDO_ATUALIZACAO', JSON.stringify(bloco));
		
		push(montarURN("projeto/conteudo/atualizacao", gerarQueryString({
			tituloProjeto: titulo,
			codigoProjeto: codigo
		})));
	}
</script>

<TBox 
	titulo="Atualização" 
	subtitulo="Formulário de atualização de projeto" 
	largura="12" 
	deslocamento="0" 
	icone="projeto">

	<form method="post" on:submit|preventDefault={submeterFormulario}>
		<p class="has-text-primary mb-4">Código do Projeto: <strong>{formatarCodigo(projeto?.codigo ?? '000000000000')}</strong></p>

		<div class="columns">
			<div class="column">
				<small>Título do projeto (mín. 8 e máx. 100 caracteres ● Utilizados: {totalCaracteresTitulo}):</small>
				<div class="field">
					<input 
						bind:value={titulo}
						name="titulo" 
						type="text" 
						placeholder="Título do projeto" 
						class="input is-large is-primary" required>
				</div>
			</div>
		</div>

		<div class="columns">
			<div class="column">
				<small>Área de atuação do projeto:</small>
				<div class="select is-primary is-large is-fullwidth">
					<select bind:value={alocacao} name="unidade" required>
						<option value="">Selecione uma...</option>
						{#each Object.entries(areasConhecimento) as [chave, valor]}
						<option value="{chave}">{valor}</option>
						{/each}
					</select>
				</div>
			</div>
		</div>

		<div class="columns">
			<div class="column is-half">
				<small>Campus onde será desenvolvido o projeto:</small>
				<div class="select is-primary is-large is-fullwidth">
					<select bind:value={instituicao} name="unidade" required>
						<option value="">Unidade...</option>
						{#each Object.entries(instituicoes) as [chave, valor]}
						<option value="{chave}">{valor}</option>
						{/each}
					</select>
				</div>
			</div>

			<div class="column is-half">
				<small>Curso:</small>
				<div class="select is-primary is-large is-fullwidth">
					<select bind:value={curso} name="unidade" required>
						<option value="">Selecione...</option>
						{#each Object.entries(cursos) as [chave, valor]}
						<option value="{chave}">{valor}</option>
						{/each}
					</select>
				</div>
			</div>
		</div>
		
		<div class="columns">
			<div class="column">
				<small>A pesquisa envolve a participação direta ou indireta de seres humanos ou experimento com animais?</small>
				<div class="control">
					<label class="radio">
						<input type="radio" bind:group={envolvePesquisaAnimaisOuHumanos} value={1} on:change={exibirEsconderNumeroCAEE}>
						Sim
					</label>
					<label class="radio">
						<input type="radio" bind:group={envolvePesquisaAnimaisOuHumanos} value={0} on:change={exibirEsconderNumeroCAEE}>
						Não
					</label>
				</div>
			</div>
		</div>

		<div class="columns is-hidden" id="divNumeroCAAE">
			<div class="column is-half">
				<small>Insira o número do Certificado de Apresentação para Apreciação Ética (CAAE), emitido por um Comitê de Ética em Pesquisa (CEP) ou comprovante de submissão do projeto a uma Comissão de Ética no Uso de Animais (CEUA) ou número de protocolo de aprovação do projeto.</small>
			</div>
			<div class="column">
				<input 
					bind:value={numeroCAAE}
					type="text"
					placeholder="Número CAAE" 
					class="input is-large is-primary">
			</div>
		</div>

		<hr>
		
		<p class="is-size-4 mb-4">Conteúdo</p>

		<div class="columns">
			<div class="column">
				<div class="notification is-info is-light">Para atualizar os blocos de conteúdo do projeto, utilize a opção atualizar disponível em cada bloco/tópico.</div>
			</div>
		</div>
		
		<div class="columns">
			<div class="column">
				<div class="content">
					<table class="table is-bordered is-striped is-hoverable is-fullwidth">
						<tr>
							<th>Tópico</th>
							<th>Conteúdo cadastrado</th>
							<th>Ações</th>
						</tr>
						{#each conteudo as bloco}
						{#if bloco.conteudo != null}
						<tr>
							<td>{ bloco.titulo }</td>
							<td>{ bloco.conteudo }</td>
							<td><button on:click|preventDefault={ prepararBlocoPraAtualizacao(bloco) } class="button is-black is-small is-outlined is-fullwidth">Atualizar conteúdo</button></td>
						</tr>
						{/if}
						{:else}
						<p>carregando ...</p>
						{/each}
					</table>
				</div>
			</div>
		</div>

		<p class="has-text-centered mb-6">
			<span class="tag" title="Data de criação do projeto (adicionado no sistema)">Data cadastro: {projeto.data_criacao}</span> &bull; 
			<span class="tag" title="Data da última atualização do projeto">Última atualização: {projeto.data_ultima_atualizacao}</span>
		</p>
	
		<div class="columns">
			<div class="column">
				<p class="control">
					<a href="#/" class="button is-black is-medium is-outlined is-fullwidth">Início</a>
				</p>
			</div>
			<div class="column">
				<p class="control">
					<a href="#/painel" class="button is-black is-medium is-outlined is-fullwidth">Painel</a>
				</p>
			</div>
			<div class="column is-8">
				<p class="control">
					<button 
						type="submit" 
						class="button is-primary is-medium is-fullwidth">
						Atualizar informações gerais do Projeto
					</button>
				</p>
			</div>
		</div>
	</form>
</TBox>