<script>
	import { formatarCPF } from './../../utils/CPF.js';
	import { formatarCodigo } from './../../utils/Projeto.js';
	import { replace, push, querystring } from 'svelte-spa-router';
	import { createEventDispatcher, onMount } from 'svelte';
	import TBoxInterno from '../templates/TBoxInterno.svelte';
	import BarraOperacoesRegistro from '../../componentes/BarraOperacoesRegistro.svelte';
	import { sTituloPaginaAtual } from '../../stores';
    import { gerarQueryString, montarURN, recuperarValorParametroQS, verificarTimestampQueryString } from '../../utils/QueryString';
    import { parseJWT } from '../../utils/JWT';
    import { API, cabecalhoRequisicao } from '../../utils/Requisicao';
    import TipoEvento from '../../constantes/TipoEvento';
    import TextoFeedback from '../../constantes/TextoFeedback';
    import StatusHttp from '../../constantes/StatusHttp';
    import TipoAcesso from '../../constantes/TipoAcesso';
	import { ativarLinksEmTexto, rolarPagina } from '../../utils/HTML';
	import TipoAnexavel from '../../constantes/TipoAnexavel';

	const dispatch = createEventDispatcher();

	let projeto = {};
	let opcoesOperacoes = [];
	let opcoesOperacoesTopo = [];
	let opcoesOperacoesRodape = [];
	let participantes = [];
	let participantesComplementares = [];
	// let avaliacoes = [];
	// let distribuicoes = [];
	let anexos = [];
	let conteudo = [];
	let token;

	onMount(async () => {
		dispatch('routeEvent', {
			tipo: TipoEvento.ESTA_CARREGANDO,
			valor: true,
			mensagem: 'Carregando projeto ...',
		});
		
		let parametrosURL = new URLSearchParams($querystring);
		verificarTimestampQueryString(parametrosURL.get('q'));
		let codigo = recuperarValorParametroQS(parametrosURL.get('q'), 'codigoProjeto');
		
		$sTituloPaginaAtual = `Visualização de Projeto (cód.: ${formatarCodigo(codigo)})`;
		
		token = parseJWT(sessionStorage.getItem('ECONRIO_TOKEN')); 

		const resposta = await fetch(API(`projetos/${codigo}`), {
			method: 'GET',
			headers: cabecalhoRequisicao()
		}).catch(erros => {
			console.log(erros);
			
			dispatch('routeEvent', {
				tipo: TipoEvento.ABRIR_MODAL,
				titulo: 'Erro',
				conteudo: TextoFeedback.ERRO_REQUISICAO
			});

			replace("#/painel");
		});

		const { data } = await resposta.json();

		if (resposta.status == StatusHttp.OK) {
			projeto = data;

			participantes = [...projeto.participantes];
			participantesComplementares = [...projeto.participantes_complemento];

			// remove o primeiro elemento do array de participantes complementares pois virá vazio devido a referencia duplicada do orientador no back-end
			participantesComplementares.shift();

			// avaliacoes = projeto.avaliacoes;
			// distribuicoes = projeto.distribuicoes;
			anexos = projeto.anexos;
			conteudo = projeto.conteudo;

			switch (token.tipo_em_uso) {
				case TipoAcesso.DOCENTE:
					opcoesOperacoes.push(
						{
							titulo: 'Adicionar anexo',
							callback: () => push(montarURN('formularios/anexo/cadastro', gerarQueryString({ 
								tipoAnexavel: TipoAnexavel.PROJETO,
								identificadorAnexavel: projeto.codigo,
							})))
						},
						{
							titulo: 'Atualizar',
							callback: () => push(montarURN('projeto/atualizacao', gerarQueryString({ codigoProjeto: projeto.codigo })))
						}, 
					);
				break;

				case TipoAcesso.DISCENTE:
					// opcoesOperacoes.push(
					// 	{
					// 		titulo: 'Adicionar Relatório',
					// 		cor: 'is-success',
					// 		callback: () => push(`#/projeto/relatorios/cadastro/${projeto.codigo}/${projeto.id}/${projeto.modalidade_const}?tp=${window.btoa(unescape(encodeURIComponent(projeto.titulo)))}`)
					// 	},
					// );
				break;

				case TipoAcesso.ADMINISTRADOR:
					// opcoesOperacoes.push(

					// );

					opcoesOperacoes.push({
						titulo: 'Adicionar anexo',
						callback: () => push(montarURN('formularios/anexo/cadastro', gerarQueryString({ 
							tipoAnexavel: TipoAnexavel.PROJETO,
							identificadorAnexavel: projeto.codigo,
						})))
					});
				break;
			}
		} else {
			dispatch('routeEvent', { 
				tipo: TipoEvento.ESTA_CARREGANDO,
				valor: false
			});
			
			replace('#/projeto-inexistente');
		}

		dispatch('routeEvent', { 
			tipo: TipoEvento.ESTA_CARREGANDO,
			valor: false
		});

		opcoesOperacoesTopo = [ ... opcoesOperacoes];
		opcoesOperacoesRodape = [ ... opcoesOperacoes];

		opcoesOperacoesTopo.push({
			titulo: 'Painel',
			cor: 'is-black is-outlined',
			callback: () => push('#/painel')
		}, {
			titulo: 'Rolar para baixo',
			cor: 'is-black is-outlined',
			callback: () => rolarPagina(document.body.scrollHeight)
		});

		opcoesOperacoesRodape.push({
			titulo: 'Rolar para cima',
			cor: 'is-black is-outlined',
			callback: () => rolarPagina(0)
		});
	});

	const exibirAnexoProjeto = (objetoAnexo) => {
		let conteudoModal = "";
		
		conteudoModal += ativarLinksEmTexto(objetoAnexo.valor);
		conteudoModal += `<hr><p class="mt-2 mb-2 has-text-info">Outras Informações:</p>`;
		conteudoModal += `<p>Adicionado em: ${objetoAnexo.adicionado_em}</p>`;
		
		dispatch('routeEvent', {
			tipo: TipoEvento.ABRIR_MODAL,
			titulo: objetoAnexo.tipo,
			larguraMaxima: objetoAnexo.valor.trim().length > 64 ? true : false,
			conteudo: conteudoModal,
		});
	}
	
	// const removerAnexo = async (idAnexo) => removerAnexoGenerico(idAnexo, dispatch, push, '#/painel/projetos', 'projetos/anexos/remover');

	
	const exibirDadosComplementaresParticipantes = (objetoComplemento) => {
		let conteudoModal = "";
		
		conteudoModal += `
			<p class="has-text-primary">E-mail: <strong>${ objetoComplemento?.email }</strong></p>
			<p class="has-text-primary">Matrícula: <strong>${ objetoComplemento?.matricula }</strong></p>
			<p class="has-text-primary">CPF: <strong>${ formatarCPF(objetoComplemento?.cpf) }</strong></p>
			<p class="has-text-primary">Coeficiente de rendimento acumulado (CRA): <strong>${ objetoComplemento?.cra }</strong></p>
			<p class="has-text-primary">Campus: <strong>${ objetoComplemento?.campus }</strong></p>
			<hr>
			<p class="has-text-primary">Previsão de conclusão do curso: <strong>${ new Date(objetoComplemento?.previsao_conclusao_curso).toLocaleDateString() }</strong></p>
			<p class="has-text-primary">Aluno(a) já participa oficialmente de Programa de Iniciação Cientifica? <br> <strong>${ objetoComplemento?.participa_pic == true ? 'SIM' : 'NÃO' }</strong></p>
			<p class="has-text-primary">Aluno(a) possui outro tipo de bolsa ou desconto? <br> <strong>${ objetoComplemento?.possui_bolsa_desconto == true ? 'SIM |' : 'NÃO' } ${ objetoComplemento?.nome_bolsa_desconto ?? "" }</strong></p>
			<p class="has-text-primary">Caso o projeto seja aprovado e ocorra a impossibilidade de usufruir a bolsa de Iniciação Científica, aluno(a) deseja permanecer no projeto como voluntário? <br> <strong>${ objetoComplemento?.aceita_voluntariado == true ? 'SIM' : 'NÃO' }</strong></p>
		`;
						
		dispatch('routeEvent', {
			tipo: TipoEvento.ABRIR_MODAL,
			titulo: objetoComplemento?.nome,
			conteudo: conteudoModal
		});
	}
</script>

<TBoxInterno
	titulo={projeto.titulo} 
	subtitulo="Campus: {projeto.instituicao} ✦ Alocação: {projeto.alocacao} ✦ Curso: {projeto.curso}"
	largura="12" 
	deslocamento="0">

	<br>
	
	<BarraOperacoesRegistro opcoes={ opcoesOperacoesTopo } />

	<div class="columns">
		<div class="column has-text-centered">
			<small>Os anexos e demais informações vinculadas ao projeto ficam no rodapé do projeto. Clique em <strong>"Rolar para baixo"</strong> para ver.</small>
		</div>
	</div>
	<hr>
	
	<span class="tag is-black is-pulled-right" title="Código do Projeto">Cód.: { formatarCodigo(projeto.codigo) }</span>
	{#if projeto?.envolve_animais_humanos == "SIM"}
	<span class="tag is-success is-pulled-right mr-2" title="Número CAAE">CAAE: { formatarCodigo(projeto.numero_caae) }</span>
	{/if}
	
	<p class="is-size-4 mb-4">Descrição</p>
	<div class="content">
		{#each conteudo as bloco}
			<small class="has-text-info">{ bloco.titulo }</small>
			{#if bloco.conteudo == null && token.tipo_em_uso == TipoAcesso.DOCENTE}
			<a href="{montarURN('projeto/conteudo/cadastro', gerarQueryString({ 
				idBloco: bloco.id,
				idPivot: bloco.id_pivot,
				codigo: projeto.codigo,
				tituloProjeto: projeto.titulo.substr(0, 31) + "..." }))}" class="button is-fullwidth is-primary mb-4">Preencher</a>
			{:else}
			<p class="mb-4">{ bloco.conteudo ?? 'Não preenchido' }</p>
			{/if}
		{:else}
			<p>carregando ...</p>
		{/each}
	</div>

	<p class="has-text-centered">
		<span class="tag" title="Data de criação do projeto (adicionado no sistema)">Data cadastro: {projeto.data_criacao}</span> &bull; 
		<span class="tag" title="Data da última atualização do projeto">Última atualização: {projeto.data_ultima_atualizacao}</span>
	</p>

	<br>

	{#if anexos?.length > 0}
	<p class="is-size-4 mb-4">Anexos</p>

	<table class="table is-fullwidth is-hoverable">
		<thead>
			<tr>
				<th>#</th>
				<th>Tipo</th>
				<th>Valor</th>
				<th class="has-text-right" title="Operações disponíveis">Operações</th>
			</tr>
		</thead>
		<tbody>
			{#each anexos as anexo, i}
			<tr>
				<th>{ ++i }</th> 
				<td>{ anexo.tipo }</td>
				{#if (anexo.valor.trim().length > 64)}
				<td>{ anexo.valor_resumo.trim() }...</td>
				{:else}
				<td>{ anexo.valor.trim() }</td>
				{/if}
				<td class="has-text-right">
					<button on:click={ () => exibirAnexoProjeto(anexo) } class="button is-success is-small">Ler</button>
				</td>
			</tr>
			{/each}
		</tbody>
	</table>
	{/if}

	{#if participantes?.length > 0}

	<p class="is-size-4 mb-4">Orientadores</p>

	<table class="table is-fullwidth is-hoverable">
		<thead>
			<tr>
				<th>#</th>
				<th>Nome</th>
				<th>E-mail</th>
				<th>Papel</th>
			</tr>
		</thead>
		<tbody>
			{#each participantes as participante, i}
			<tr class="{participante.ativo != true ? 'riscar-linha' : ''}">
				<th>{ ++i }</th> 
				{#if participante.ativo == true}
				<td><a href="{montarURN('usuario/visualizacao', gerarQueryString({ cpf: participante.cpf }))}">{ participante.nome }</a></td>
				{:else}
				<td>{ participante.nome }</td>
				{/if}

				<td>{ participante.email }</td>
				<td>{ participante.papel }</td>
			</tr>
			{/each}
		</tbody>
	</table>
	{/if}
	
	{#if participantesComplementares?.length > 0}

	<p class="is-size-4 mb-4">Participantes</p>

	<table class="table is-fullwidth is-hoverable">
		<thead>
			<tr>
				<th>#</th>
				<th>Nome</th>
				<th>E-mail</th>
				<th>Papel</th>
				<th>Data indicação</th>
				<th>Ações</th>
			</tr>
		</thead>
		<tbody>
			{#each participantesComplementares as participante, i}
			<tr class="{participante.ativo != true ? 'riscar-linha' : ''}">
				<th>{ ++i }</th> 
				{#if participante.ativo == true}
				<td>{ participante?.complemento?.nome }</td>
				{:else}
				<td>{ participante?.complemento?.nome }</td>
				{/if}

				<td>{ participante?.complemento?.email }</td>
				<td>{ participante.papel }</td>
				<td>{ participante.data_indicacao }</td>
				<td><button class="button is-black is-outlined" on:click|preventDefault={exibirDadosComplementaresParticipantes(participante?.complemento)}>Ver cadastro</button></td>
			</tr>
			{/each}
		</tbody>
	</table>
	{/if}
	
	<hr>

	<BarraOperacoesRegistro opcoes={ opcoesOperacoesRodape } />
</TBoxInterno>