import StatusHttp from "../constantes/StatusHttp";
import TipoEvento from "../constantes/TipoEvento";
import { gerarQueryString, montarURN } from "./QueryString";
import { API, cabecalhoRequisicao } from "./Requisicao";
import { formatarCodigo } from '../utils/Projeto';
import { formatarCPF } from "./CPF";

export const listarObjetosAPI = async (
	endpoint,
	dispatchCallback, 
	replaceCallback,
	rotaReplaceFalha) => {

	let objetoRetorno = {};

	dispatchCallback('routeEvent', {
		tipo: TipoEvento.ESTA_CARREGANDO,
		valor: true,
		mensagem: 'Carregando dados ...',
	});  

	const resposta = await fetch(API(endpoint), {
		method: 'GET',
		headers: cabecalhoRequisicao(),
	}).catch(erro => { throw new Error(`[${ erro.message }] - ${ TextoFeedback.ERRO_REQUISICAO }`) });
	
	const json = await resposta.json();

	if (resposta.status == StatusHttp.OK) {
		let { dados: { data, meta } } = json;

		objetoRetorno = {
			dados: data,
			paginacao: {
				paginaAtual: meta.current_page,
				paginaAnterior: meta.current_page - 1,
				proximaPagina: meta.current_page + 1,
				totalResultados: meta.total,
				ultimaPagina: meta.last_page,
				resultadosPorPagina: meta.per_page
			}
		};
	} else if (resposta.status == StatusHttp.HTTP_NOT_FOUND) {
		dispatchCallback('routeEvent', {
			tipo: TipoEvento.ABRIR_MODAL,
			titulo: 'Aviso!',
			conteudo: json.mensagem ?? 'Nenhum dado encontrado.'
		});
	} else throw new Error(json.mensagem ?? 'Falha ao recuperar dados.');

	dispatchCallback('routeEvent', { 
		tipo: TipoEvento.ESTA_CARREGANDO,
		valor: false
	});

	return objetoRetorno;
}

export const usuarioListagemDTO = (usuario) => {
	return {
		tag: usuario.tiposAcesso,
		link: montarURN('usuario/visualizacao', gerarQueryString({ cpf: usuario.cpf })),
		titulo: usuario.nome,
		descricao: `CPF: ${formatarCPF(usuario.cpf)} / E-mail: ${usuario.email}`
	}
}

export const projetoListagemDTO = (projeto) => {
	return {
		tag: formatarCodigo(projeto.codigo),
		link: montarURN('projeto/visualizacao', gerarQueryString({ 
			codigoProjeto: projeto.codigo
		})),
		titulo: projeto.titulo,
		descricao: `Campus ${projeto.instituicao} | Curso ${projeto.curso}`
	}
}

export const projetoListagemAnonimizadaDTO = (projeto) => {
	return {
		tag: '...',
		link: montarURN('projeto/visualizacao/anonimizado', gerarQueryString({ codigoProjeto: projeto.codigo })),
		titulo: projeto.titulo,
		descricao: `Unidade ${projeto.unidade}`
	}
}

export const avaliacaoObjetoListagemDTO = (avaliacao) => {
	return {
		tag: avaliacao.finalizada == true ? 'FINALIZADA' : 'PENDENTE',
		link: montarURN('avaliacao/visualizar', gerarQueryString({ idAvaliacao: avaliacao.id })),
		titulo: avaliacao.objetoAvaliado.hasOwnProperty('titulo') ? `Proj.: ${avaliacao.objetoAvaliado.titulo}` : `Currículo: ${avaliacao.objetoAvaliado.codigo}`,
		descricao: `Avaliado por: ${avaliacao.avaliador.nome} # em ${avaliacao.data_ultima_atualizacao}`
	}
}

// para avaliadores
export const avaliacaoObjetoListagemAnonimizadaDTO = (avaliacao) => {
	let curriculo = {
		tag: avaliacao.finalizada == true ? 'FINALIZADA' : 'PENDENTE',
		link: avaliacao.finalizada 
			? montarURN('avaliacao/visualizar', gerarQueryString({ idAvaliacao: avaliacao.id }))
			: montarURN('curriculo/visualizar/anonimizado', gerarQueryString({ codigoCurriculo: avaliacao.objetoAvaliado.codigo, idAvaliacao: avaliacao.id })),
		titulo: `Currículo: ${avaliacao.objetoAvaliado.codigo}`,
		descricao: `Avaliador: ${avaliacao.avaliador.nome} # em ${avaliacao.data_ultima_atualizacao}`
	};
	
	let projeto = {
		tag: avaliacao.finalizada == true ? 'FINALIZADA' : 'PENDENTE',
		link: avaliacao.finalizada 
			? montarURN('avaliacao/visualizar', gerarQueryString({ idAvaliacao: avaliacao.id }))
			: montarURN('projeto/visualizacao/anonimizado', gerarQueryString({ codigoProjeto: avaliacao.objetoAvaliado.codigo, idAvaliacao: avaliacao.id })),
		titulo: `Projeto: ${avaliacao.objetoAvaliado.titulo}`,
		descricao: `Avaliador: ${avaliacao.avaliador.nome} # em ${avaliacao.data_ultima_atualizacao}`
	};

	return avaliacao.objetoAvaliado.hasOwnProperty('titulo') ? projeto : curriculo;
}

export const afirmacaoListagemDTO = (afirmacao) => {
	return {
		tag: afirmacao.formularios,
		acoes: [{
			url: afirmacao.ativa == true 
				? montarURN('banco-afirmacoes/ativar-desativar', gerarQueryString({ idAfirmacao: afirmacao.id, operacao: 'DESATIVAR' }))
				: montarURN('banco-afirmacoes/ativar-desativar', gerarQueryString({ idAfirmacao: afirmacao.id, operacao: 'ATIVAR' })),
			texto: afirmacao.ativa == true ? 'Desativar' : 'Ativar'
		}],
		titulo: afirmacao.titulo,
		descricao: `Cadastrada em ${afirmacao.data_criacao} | Ativa: ${afirmacao.ativa == true ? 'SIM' : 'NÃO'}`
	}
}