<script>
	import { createEventDispatcher, onMount } from 'svelte';
	import { replace, querystring } from 'svelte-spa-router';
    import { recuperarValorParametroQS } from '../../utils/QueryString';
    import TipoEvento from '../../constantes/TipoEvento';
	import { API, cabecalhoRequisicao } from '../../utils/Requisicao';
    import TextoFeedback from '../../constantes/TextoFeedback';
    import StatusHttp from '../../constantes/StatusHttp';
	
	const dispatch = createEventDispatcher();
	
	onMount(async () => {
		let parametrosURL = new URLSearchParams($querystring);
		let mensagem = recuperarValorParametroQS(parametrosURL.get('q'), 'mensagem');
		
		dispatch('routeEvent', {
			tipo: TipoEvento.ESTA_CARREGANDO,
			valor: true,
			mensagem: mensagem,
		});

		const resposta = await fetch(API('refresh'), {
			method: 'POST',
			headers: cabecalhoRequisicao()
		}).catch(erros => {
			console.log(erros);

			dispatch('routeEvent', {
				tipo: TipoEvento.ABRIR_MODAL,
				titulo: 'Erro',
				conteudo: TextoFeedback.ERRO_REQUISICAO
			});

			replace("/login");
		});
		
		const json = await resposta.json();

		if (resposta.status == StatusHttp.OK) {
			sessionStorage.removeItem('ECONRIO_TOKEN');
			sessionStorage.setItem('ECONRIO_TOKEN', json.jwt.token);
			dispatch('routeEvent', {
				tipo: TipoEvento.ABRIR_MODAL,
				titulo: 'Aviso',
				conteudo: 'Token renovado com sucesso.'
			});

			replace("/painel");
		} else {
			dispatch('routeEvent', {
				tipo: TipoEvento.ABRIR_MODAL,
				titulo: 'Erro',
				conteudo: 'Falha ao renovar o token de acesso. Necessário realizar login novamente.'
			});
			
			replace("/login");
		}

		dispatch('routeEvent', { 
			tipo: TipoEvento.ESTA_CARREGANDO,
			valor: false
		});
	});
</script>
