<script>
	import { createEventDispatcher, onMount } from 'svelte';
	import { replace } from 'svelte-spa-router';
	import PainelBox from '../templates/PainelBox.svelte';
	import GradeCartoes from '../../componentes/GradeCartoes.svelte';
	import CartaoRetangulo from '../../componentes/cartoes/CartaoRetangulo.svelte';
    import TipoEvento from '../../constantes/TipoEvento';
	
	const dispatch = createEventDispatcher();
	let parseConfiguracoes = [];

	onMount(async () => {
		if (verificarLocalStorage('ECONRIO_TOKEN'))
			carregarConfiguracoesApp();
		else 
			replace("/login");
	});

	const carregarConfiguracoesApp = async () => {
		parseConfiguracoes = [];

		dispatch('routeEvent', {
			tipo: TipoEvento.ESTA_CARREGANDO,
			valor: true,
			mensagem: 'Carregando configurações ...',
		});

		Object.keys(localStorage)
			.filter( nomeChave => nomeChave.includes('ECONRIO_CONFIG_APP_'))
			.forEach(variavel => {
				parseConfiguracoes.push({
					acoes: [{
						url: montarURN('painel/configuracoes/atualizacao', gerarQueryString({ 
							variavel: variavel,
							escopo: 'LOCAL'
						})),
						texto: 'Configurar'
					}],
					titulo: variavel,
					descricao: `Valor = ${localStorage.getItem(variavel)}`
				});				
			});

		dispatch('routeEvent', { 
			tipo: TipoEvento.ESTA_CARREGANDO,
			valor: false
		});
	}
</script>

<PainelBox 
	largura="12" 
	deslocamento="0"
	titulo="Configurações da Aplicação">
	
	<GradeCartoes itens={parseConfiguracoes} componente={CartaoRetangulo} flexWrap={false} />
</PainelBox>
