<script>
	import { createEventDispatcher, onMount } from 'svelte';
	import { replace } from 'svelte-spa-router';
    import TipoEvento from '../../constantes/TipoEvento';
    import { API } from '../../utils/Requisicao';
	import TBox from '../templates/TBox.svelte';
	import StatusHttp from '../../constantes/StatusHttp';
    import TextoFeedback from '../../constantes/TextoFeedback';

	const dispatch = createEventDispatcher();

	let cpf = "";
	let senha = "";
	let tipoUsuario = "";
	let tiposUsuario = [];
	let tituloAviso = "Erro";

	onMount(async () => {
		let informacosAPICache = JSON.parse(localStorage.getItem('ECONRIO_API_INFO_CACHE'));
		tiposUsuario = Object.entries(informacosAPICache?.tipos_usuarios);
	});

	async function submeterFormulario() {
		dispatch('routeEvent', {
			tipo: TipoEvento.ESTA_CARREGANDO,
			valor: true,
			mensagem: 'Aguarde ...',
		});

		// faz a chamada a API no endpoint de login, envia os dados e aguarda a resposta
		const resposta = await fetch(API('login'), {
			method: 'POST',
			headers: {
				'Accept': 'application/json',
				'Content-Type': 'application/json'
			},
			body: JSON.stringify({
				cpf: cpf.trim(), 
				senha: senha,
				tipoAcesso: tipoUsuario
			})
		}).catch(erros => {
			console.log(erros);

			dispatch('routeEvent', {
				tipo: TipoEvento.ESTA_CARREGANDO,
				valor: false
			});
			
			dispatch('routeEvent', {
				tipo: TipoEvento.ABRIR_MODAL,
				titulo: tituloAviso,
				conteudo: TextoFeedback.ERRO_REQUISICAO
			});
		});

		const json = await resposta.json();

		if (resposta.status == StatusHttp.OK) {
			
			// verifica inicialmente se token do usuário está bloqueado
			if (json.usuario?.bloqueado == true) {
				dispatch('routeEvent', {
					tipo: TipoEvento.ABRIR_MODAL,
					titulo: 'Acesso bloqueado.',
					conteudo: 'Seu acesso está bloqueado, favor entrar em contato com o suporte.'
				});

				limparCacheAplicacao(true, true, true);
				setTimeout( () => replace('/'), 1000);
			} else {
				// guarda o token no navegador do usuário (session storage)
				sessionStorage.setItem('ECONRIO_TOKEN', json.jwt.token);

				// aguarda um pouco antes de redirecionar devido o localstorage levar um tempo pra escrever (síncrono)
				setTimeout(() => {
					// redireciona pro painel do usuário
					replace('/painel');
				}, 1000);
			}

		} else {
			dispatch('routeEvent', {
				tipo: TipoEvento.ABRIR_MODAL,
				titulo: tituloAviso,
				conteudo: json?.mensagem
			});
		}

		dispatch('routeEvent', {
			tipo: TipoEvento.ESTA_CARREGANDO,
			valor: false
		});
	}
</script>

<TBox 
	titulo="Login" 
	subtitulo="Insira seus dados para continuar" 
	icone="login">
	
	<form method="post" on:submit|preventDefault={submeterFormulario}>
		<small class="has-text-primary">Tipo de acesso:</small>
		<div class="field">
			<div class="select is-primary is-large is-fullwidth">
				<select bind:value={tipoUsuario} name="tipoUsuario" required>
					<option value="">Escolha um tipo...</option>
					{#each tiposUsuario as [chave, valor]}
					<option value="{chave}">{valor}</option>
					{/each}
				</select>
			</div>
		</div>

		<small class="has-text-primary">CPF (apenas números):</small>
		<div class="field">
			<input 
				bind:value={cpf}
				name="cpf"
				type="tel" 
				pattern="[0-9]*" 
				maxlength="11" 
				placeholder="CPF (apenas números)" 
				class="input is-large is-primary" 
				autocomplete="cpf" required>
		</div>

		<small class="has-text-primary">Senha:</small>
		<div class="field">
			<input 
				bind:value={senha}
				name="senha" 
				type="password" 
				class="input is-large is-primary" 
				placeholder="Senha" required>
		</div>

		<br>

		<div class="columns">
			<div class="column">
				<p class="control">
					<a href="#/" class="button is-black is-outlined is-medium is-fullwidth">Voltar</a>
				</p>
			</div>
			<div class="column is-8">
				<p class="control">
					<button 
						type="submit" 
						class="button is-primary is-medium is-fullwidth">
						Entrar
					</button>
				</p>
			</div>
		</div>
	</form>
</TBox>