		-ms-user-select: none; /* IE10+/Edge */
		user-select: none; /* Standard */
		break-inside: avoid-column;
		border:3px solid #fff;
	}

	.box:hover {
		border:3px solid #7700b8;
	}

	.borda-noite {
		border: 3px solid #aaa;
	}

	.cor-titulo-noite {
		color: #2e84fd;
	}
</style>

