import TipoEvento from '../constantes/TipoEvento';
import StatusHttp from '../constantes/StatusHttp';
import TextoFeedback from '../constantes/TextoFeedback';

export const API = endpoint => "https://econrio-api-teste.rbssd.repl.co/" + endpoint;
// export const API = endpoint => "http://localhost:8000/api/" + endpoint;

export const cabecalhoRequisicao = () => {
	return {
		Accept: 'application/json',
		'Content-Type': 'application/json',
		Authorization:  `Bearer ${sessionStorage.getItem('ECONRIO_TOKEN')}`,
		'Ano-Base-Request': localStorage.getItem('ECONRIO_CONFIG_APP_ANOBASE'),
	}
}

// retorna true se operação foi realizada com sucesso, false caso contrário
export const enviarDadosFormulario = async (
	metodo,
	dispatchCallback, 
	replaceCallback, 
	endpoint, 
	corpoRequisicao, 
	mensagemSucesso,
	rotaReplaceSucesso,
	rotaReplaceFalha, 
	uploadArquivo = false) => {
	
	let tituloAviso = "Erro...";
	let feedbackUsuario = [];
	let retornoSucesso = true;

	dispatchCallback('routeEvent', {
		tipo: TipoEvento.ESTA_CARREGANDO,
		valor: true,
		mensagem: 'Enviando os dados ...',
	});
	
	// faz a chamada a API no endpoint informado, envia os dados e aguarda a resposta
	const resposta = await fetch(API(endpoint), {
		method: metodo,
		headers: uploadArquivo ? cabecalhoRequisicaoUploadArquivo() : cabecalhoRequisicao(),
		body: corpoRequisicao
	}).catch(erros => {
		console.error(erros);
		
		dispatchCallback('routeEvent', { 
			tipo: TipoEvento.ESTA_CARREGANDO,
			valor: false
		});

		dispatchCallback('routeEvent', {
			tipo: TipoEvento.ABRIR_MODAL,
			titulo: 'Erro na requisição',
			conteudo: TextoFeedback.ERRO_REQUISICAO
		});

		retornoSucesso = false;
	});

	// pega o json do retorno da resposta
	const json = await resposta.json();

	if (resposta.status == StatusHttp.CREATED || resposta.status == StatusHttp.OK) {
		tituloAviso = 'Operação realizada com sucesso!'
		feedbackUsuario.push(mensagemSucesso);
		replaceCallback(rotaReplaceSucesso);
	} else if (resposta.status == StatusHttp.UNPROCESSABLE_ENTITY) {
		tituloAviso = 'Verifique os dados informados!'
		feedbackUsuario.push(json?.mensagem);
	} else {
		feedbackUsuario.push(json?.mensagem);
		if (rotaReplaceFalha != null) replaceCallback(rotaReplaceFalha);
		retornoSucesso = false;
	};

	dispatchCallback('routeEvent', { 
		tipo: TipoEvento.ESTA_CARREGANDO,
		valor: false
	});

	// exibe feedback pro usuário se existir algo pra exibir
	if (feedbackUsuario[0]) {
		dispatchCallback('routeEvent', {
			tipo: TipoEvento.ABRIR_MODAL,
			titulo: tituloAviso,
			conteudo: feedbackUsuario[0]
		});
	}

	// limpa as mensagens
	feedbackUsuario = [];

	return retornoSucesso;
}