<script>
	import { onMount } from 'svelte';
	import { querystring } from 'svelte-spa-router';
    import { recuperarValorParametroQS } from '../utils/QueryString';
	import TBox from './templates/TBox.svelte';

	let mensagem, titulo, subtitulo = null;

	onMount( () => {
		let parametrosURL = new URLSearchParams($querystring);
		titulo = recuperarValorParametroQS(parametrosURL.get('q'), 'titulo') ?? 'Não Encontrado';
		subtitulo = recuperarValorParametroQS(parametrosURL.get('q'), 'subtitulo') ?? 'A página ou recurso solicitado não existe.';
		mensagem = recuperarValorParametroQS(parametrosURL.get('q'), 'mensagem');
	})
</script>

<TBox 
	titulo={ titulo } 
	largura="6" 
	deslocamento="3" 
	correcaoY="-13px"
	icone="aviso">

	<div class="has-text-centered">
		<p class="is-size-1">{ subtitulo }</p>
		{#if mensagem }
		<p class="my-2">Mensagem: </p>
		<div class="notification is-black is-family-monospace has-text-left">{ mensagem }</div>
		<p class="has-text-primary mt-2">Se o erro persistir, favor contactar suporte e informar a mensagem acima.</p>
		{/if}
	</div>

	<hr>

	<br>

	<div class="has-text-centered">
		<div class="columns">
			<div class="column">
				<a href="#/" class="button is-black is-outlined is-medium is-fullwidth pb">Início</a>
			</div>
			<div class="column">
				<a href="#/suporte" class="button is-primary is-medium is-fullwidth pb">Suporte</a>
			</div>
		</div>
	</div>
</TBox>