<script>
	import { createEventDispatcher, onMount } from 'svelte';
	import { replace } from 'svelte-spa-router';
	import GradeCartoes from '../componentes/GradeCartoes.svelte';
	import BarraPaginacao from '../componentes/BarraPaginacao.svelte';
	import CartaoQuadrado from '../componentes/cartoes/CartaoQuadrado.svelte';
    import TipoEvento from '../constantes/TipoEvento';
    import TipoObjetoListagem from '../constantes/TipoObjetoListagem';
	import { 
		afirmacaoListagemDTO, 
		avaliacaoObjetoListagemAnonimizadaDTO, 
		avaliacaoObjetoListagemDTO, 
		listarObjetosAPI, 
		projetoListagemAnonimizadaDTO, 
		projetoListagemDTO, usuarioListagemDTO } from './../utils/Listagem';
    import { verificarSeExisteCacheChaveLS, verificarSessionStorage } from '../utils/Verificadores';
    import StatusHttp from '../constantes/StatusHttp';
    import { API, cabecalhoRequisicao } from '../utils/Requisicao';
	
	export let rotaErro = "/";
	export let chaveSSToken = "ECONRIO_TOKEN";
	export let chaveLSCache;
	export let endpointRecurso;
	export let tipoObjeto;
	export let parametros;
	export let componenteCartao = CartaoQuadrado;
	export let flexWrapFlagGrade = true;
	export let exibirOpcaoExportar = false;
	export let endpointExportacao = "";
	export let tipoMIME = "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet";

	const dispatch = createEventDispatcher();
	let parseObjetos = [];
	let paginacaoComponente;

	onMount(async () => {
		if (verificarSessionStorage(chaveSSToken)) await carregarObjetosAPI(1);
		else replace(rotaErro);
	});

	const ajustarListagemObjetos = (listaObjetos) => {
		let objetoPushCallback;

		switch (tipoObjeto) {
			case TipoObjetoListagem.PROJETO:
				objetoPushCallback = projetoListagemDTO;
				break;
			case TipoObjetoListagem.PROJETO_ANON:
				objetoPushCallback = projetoListagemAnonimizadaDTO;
				break;
			case TipoObjetoListagem.AVALIACAO:
				objetoPushCallback = avaliacaoObjetoListagemDTO; 
				break;
			case TipoObjetoListagem.AVALIACAO_ANON:
				objetoPushCallback = avaliacaoObjetoListagemAnonimizadaDTO;
				break;
			case TipoObjetoListagem.AFIRMACAO:
				objetoPushCallback = afirmacaoListagemDTO;
				break;
			default:
				objetoPushCallback = usuarioListagemDTO;
				break;
		}
		
		listaObjetos.forEach( objeto => parseObjetos.push(objetoPushCallback(objeto)));
	}

	const callbackCarregarObjetos = async (pagina) => {
		if (verificarSeExisteCacheChaveLS(pagina, chaveLSCache)) carregarObjetosCache(pagina);
		else await carregarObjetosAPI(pagina);
	}
	
	const carregarObjetosCache = (pagina) => {
		let { dados, paginacao } = JSON.parse(localStorage.getItem(chaveLSCache + pagina));

		// limpa o array
		parseObjetos = [];
		
		paginacaoComponente = paginacao;
		ajustarListagemObjetos(dados);
		
		// espalha os objetos no array
		parseObjetos = [...parseObjetos];
	}

	const carregarObjetosAPI = async (pagina) => {
		// limpa o array
		parseObjetos = [];
		
		const { dados, paginacao } = await listarObjetosAPI(`${endpointRecurso}?page=${pagina}`, dispatch, replace, rotaErro);

		if (dados != undefined) {
			paginacaoComponente = paginacao;
			ajustarListagemObjetos(dados);
			
			// espalha os objetos no array
			parseObjetos = [...parseObjetos];
			
			// armazena no cache
			localStorage.setItem(chaveLSCache + paginacaoComponente.paginaAtual, JSON.stringify({
				dados: dados,
				paginacao: paginacaoComponente
			}));
		} else window.history.back();
	}

	const baixarArquivo = async () => {
		if (endpointExportacao != "") {
			dispatch('routeEvent', {
				tipo: TipoEvento.ESTA_CARREGANDO,
				valor: true,
				mensagem: 'Solicitando arquivo ...',
			});

			const resposta = await fetch(API(endpointExportacao), {
				method: 'GET',
				headers: cabecalhoRequisicao(),
			}).catch(erros => {
				console.log(erros);
				
				dispatch('routeEvent', {
					tipo: TipoEvento.ABRIR_MODAL,
					titulo: 'Erro (falha na requisição)',
					conteudo: 'Não é possível baixar o arquivo, tente novamente mais tarde.'
				});
			}).finally( () => {
				dispatch('routeEvent', { 
					tipo: TipoEvento.ESTA_CARREGANDO,
					valor: false
				});
			});
			
			const blob = await resposta.blob();

			if (resposta.status == StatusHttp.OK) {
				const b = new Blob([blob], { type: tipoMIME });
				const urlBlob = URL.createObjectURL(b);
				
				window.open(URL.createObjectURL(b), '_blank');
				URL.revokeObjectURL(urlBlob);
			} else {
				dispatch('routeEvent', {
					tipo: TipoEvento.ABRIR_MODAL,
					titulo: 'Erro',
					conteudo: 'Falha ao processar solicitação (arquivo/endpoint inválido ou erro ao gerar.).'
				});
			}
		} else {
			dispatch('routeEvent', {
				tipo: TipoEvento.ABRIR_MODAL,
				titulo: 'Erro',
				conteudo: 'Endpoint não informado.'
			});
		}
	}
</script>

<style>
	.container {
		position: fixed;
		top: 75px;
		left: -10px;
		z-index: 2;
	}

	.operacao {
		border-radius: 5px;
		cursor: pointer;
	}
</style>

<BarraPaginacao callback={callbackCarregarObjetos} { ... paginacaoComponente }/>

{#if exibirOpcaoExportar} 
<div class="container">
	<div class="operacao mt-2 p-1 pl-5 has-background-primary has-text-white" on:click|preventDefault={baixarArquivo}>
		<p>Exportar para planilha (arquivo .xlsx) &#9670;</p>
	</div>
</div>
{/if}

<GradeCartoes itens={parseObjetos} componente={componenteCartao} flexWrap={flexWrapFlagGrade} parametros={parametros} />
