import { writable } from 'svelte/store';

/*
Not all application state belongs inside your application's component hierarchy. 
Sometimes, you'll have values that need to be accessed by multiple unrelated components, 
or by a regular JavaScript module.
In Svelte, we do this with stores. A store is simply an object with a subscribe 
method that allows interested parties to be notified whenever the store value changes. 
In App.svelte, count is a store, and we're setting count_value 
in the count.subscribe callback.

https://svelte.dev/tutorial/writable-stores
*/

export const sCaptcha = writable('');
export const sTema = writable('');
export const sTituloPaginaAtual = writable('');
export const sAlgumRecursoCarregando = writable(false);
export const sHorasVidaUtilToken = writable(0);