<script>
	import { createEventDispatcher } from 'svelte';
	import { replace } from 'svelte-spa-router';
    import TipoEvento from '../constantes/TipoEvento';
	import TBox from './templates/TBox.svelte';

	const dispatch = createEventDispatcher();
	const salvarConsentimentoUsurioTermosEdital = () => {
		
		dispatch('routeEvent', { 
			tipo: TipoEvento.ESTA_CARREGANDO,
			mensagem: 'Aguarde, redirecionando ...',
			valor: true
		});
		
		localStorage.setItem('APP_CONFIRMACAO_CONSENTIMENTO', JSON.stringify({
			data_consentimento: new Date().toLocaleString()
		}));

		setTimeout( () => replace('#/'), 2000);
	}
</script>

<TBox 
	titulo="Aviso" 
	subtitulo="Leia atentamente o edital" 
	largura="6" 
	deslocamento="3" 
	correcaoY="-13px"
	icone="aviso">

	<div class="p-4 has-text-centered">
		<p class="is-size-4">Ao acessar o sistema você concorda com os 
			<br>
			<strong>TERMOS E REGRAS XPTO ...</strong>.
			<br><br>

			<a href="http://www.test.com" target="_blank">Link 1</a> | <a href="http://www.test.com" target="_blank">Link 2</a>
		</p>
		<br><br>
		<button class="button is-primary is-size-4" on:click|preventDefault={salvarConsentimentoUsurioTermosEdital}>Clique aqui para continuar</button>
	</div>
</TBox>