export const ativarLinksEmTexto = (texto) => {
	if(!texto) return;

	var urlRegex = /(((https?:\/\/)|(www\.))[^\s]+)/g;
	
	return texto.replace(urlRegex, function (url) {
		var hyperlink = url;
		
		if (!hyperlink.match('^https?:\/\/')) {
			hyperlink = 'http://' + hyperlink;
		}
		
		return '<a href="' + hyperlink + '" target="_blank">' + url + '</a>'
	});
}

export const rolarPagina = (valorPixelsY) => window.scrollTo(0, valorPixelsY);
export const gebi = (idElemento) => document.getElementById(idElemento);