<script>
	import { createEventDispatcher, onMount } from 'svelte';
	import { querystring, replace } from 'svelte-spa-router';
	import { sCaptcha } from '../../stores';
	import TBox from '../templates/TBox.svelte';
	import Captcha from '../../componentes/Captcha.svelte';
    import { gerarQueryString, montarURN, recuperarValorParametroQS, verificarTimestampQueryString } from '../../utils/QueryString';
    import TipoEvento from '../../constantes/TipoEvento';
    import { enviarDadosFormulario } from '../../utils/Requisicao';
	import TipoAnexavel from '../../constantes/TipoAnexavel';
    import { gerarStringAleatoria } from '../../utils/Geradores';

	const dispatch = createEventDispatcher();
	
	// variáveis para trabalhar com os dados do formulário
	let tipo, valor, tipoAnexavel, identificadorAnexavel, captchaUsuario;
	let tiposAnexos = [];
	let informacosAPICache = null;
	let localRedirect;

	onMount(async () => {
		informacosAPICache = JSON.parse(localStorage.getItem('ECONRIO_API_INFO_CACHE'));
		let tiposAnexosProjeto = informacosAPICache.anexos_projeto;
		let tiposAnexosUsuario = informacosAPICache.anexos_usuario;

		let parametrosURL = new URLSearchParams($querystring);
		verificarTimestampQueryString(parametrosURL.get('q'));
		
		identificadorAnexavel = recuperarValorParametroQS(parametrosURL.get('q'), 'identificadorAnexavel');
		tipoAnexavel = recuperarValorParametroQS(parametrosURL.get('q'), 'tipoAnexavel');

		if (tipoAnexavel == TipoAnexavel.USUARIO) {
			tiposAnexos = Object.entries(tiposAnexosUsuario);
			localRedirect = montarURN('usuario/visualizacao', gerarQueryString({
				cpf: identificadorAnexavel
			}));
		} else {
			tiposAnexos = Object.entries(tiposAnexosProjeto);
			localRedirect = montarURN('projeto/visualizacao', gerarQueryString({
				codigoProjeto: identificadorAnexavel
			}));
		}
	});

	async function submeterFormulario(event) {
		if (captchaUsuario != $sCaptcha) {
			dispatch('routeEvent', { 
				tipo: TipoEvento.ESTA_CARREGANDO,
				valor: false
			});
			
			dispatch('routeEvent', {
				tipo: TipoEvento.ABRIR_MODAL,
				titulo: 'Captcha Inválido',
				conteudo: 'O código informado é inválido, tente novamente.'
			});
			
			// atualiza o store
			sCaptcha.update(novaString => gerarStringAleatoria(3));
		} else {
			// faz a chamada a API no endpoint atualizar, envia os dados e aguarda a resposta
			enviarDadosFormulario('POST', dispatch, replace, 'anexos' , JSON.stringify({
				tipoAnexo: tipo,
				identificadorAnexavel: identificadorAnexavel,
				tipoAnexavel: tipoAnexavel,
				valor: valor,
				}), 'Anexo cadastrado com sucesso.', 
				localRedirect, 
				null, false
			);
		}
	}
</script>

<TBox 
	titulo="Formulário de Anexos" 
	subtitulo="Adicionando novo anexo no cadastro" 
	largura="10" 
	deslocamento="1" 
	correcaoY="-13px"
	icone="anexo">
	
	<form method="post" on:submit|preventDefault={submeterFormulario}>
		<div class="columns">
			<div class="column">
				<small class="has-text-primary">Selecione o tipo de anexo e clique e definir:</small>
				<div class="select is-primary is-large is-fullwidth">
					<select 
						bind:value={tipo} 
						name="tipo" required>
						<option value="">Escolha...</option>
						{#each tiposAnexos as [chave, valor]}
						<option value="{chave}">{valor}</option>
						{/each}
					</select>
				</div>
			</div>
		</div>

		<div>
			<div class="columns">
				<div class="column">
					<p>Digite a informação extra desejada no campo abaixo. Limite de 1024 caracteres.</p>
					<br>

					<div class="field">
						<textarea 
							bind:value={valor}
							class="textarea is-primary is-size-5" 
							placeholder="Escreva aqui o contúdo para o tipo de anexo selecionado"
							name="valor"></textarea>
					</div>
				</div>
			</div>
		</div>

		<div class="columns is-mobile">
			<div class="column">
				<Captcha tamanho={3} />
			</div>
			<div class="column">
				<input 
					name="captcha"
					bind:value={captchaUsuario}
					type="text"
					maxlength="8" 
					placeholder="Digite o código aqui" 
					class="input is-large is-primary" required>
			</div>
		</div>

		<div class="columns">
			<div class="column">
				<p class="control">
					{#if (tipoAnexavel == TipoAnexavel.USUARIO)}
					<a href="{montarURN('usuario/visualizacao', gerarQueryString({ cpf: identificadorAnexavel }))}" class="button is-black is-medium is-outlined is-fullwidth">Voltar</a>
					{:else if tipoAnexavel == TipoAnexavel.PROJETO}
					<a href="{montarURN('projeto/visualizacao', gerarQueryString({ codigoProjeto: identificadorAnexavel }))}" class="button is-black is-medium is-outlined is-fullwidth">Voltar</a>
					{:else}
					<a href="#/painel" class="button is-black is-medium is-outlined is-fullwidth">Voltar</a>
					{/if}
				</p>
			</div>
			<div class="column is-8">
				<p class="control">
					<button 
						type="submit" 
						class="button is-primary is-medium is-fullwidth">
						Adicionar
					</button>
				</p>
			</div>
		</div>
	</form>
</TBox>