<script>
	import { onMount, createEventDispatcher } from 'svelte';
	import { sTema } from '../stores';
	import { replace } from 'svelte-spa-router';
	import TBox from './templates/TBox.svelte';
	import GradeCartoes from '../componentes/GradeCartoes.svelte';
	import CartaoQuadrado from '../componentes/cartoes/CartaoQuadrado.svelte';
    import { verificarLocalStorage, verificarSessionStorage } from '../utils/Verificadores';
	import TipoEvento from '../constantes/TipoEvento';
    import { API } from '../utils/Requisicao';
	
	let icone;
	let tema;
	let exibirAvisoAceiteTermosEdital = true;

	const dispatch = createEventDispatcher();

	const funcionalidadesInicio = () => {
		let opcoes = [
			{ 
				titulo: "Suporte", 
				icone: "suporte",
				link: "#/suporte"
			}
		];
		
		if (verificarSessionStorage('ECONRIO_TOKEN')) {
			opcoes.unshift({
				titulo: "Painel", 
				icone: "painel",
				link: "#/painel"
			})
		} else {
			opcoes.unshift({
				titulo: "Login", 
				icone: "login",
				link: "#/login"
			})
		}

		return opcoes;
	}

	onMount(async () => {
		// verifica se usuario ja confirmou aceite dos termos do edital
		exibirAvisoAceiteTermosEdital = !verificarLocalStorage('APP_CONFIRMACAO_CONSENTIMENTO');

		if(exibirAvisoAceiteTermosEdital) replace("#/confirmacao-aceite-termos");

		// definições padrão das configs locais

		dispatch('routeEvent', { 
			tipo: TipoEvento.ESTA_CARREGANDO,
			mensagem: 'Configurando aplicação ...',
			valor: true
		});

		tema = $sTema;
		
		if (!tema) {
			tema = "dia";
			localStorage.setItem('ECONRIO_CONFIG_APP_TEMA', tema);

			sTema.update(novoTema => tema);
		}

		icone = (tema == "dia") ? "logo.png" : "logo-pfe.png";

		// configura app pra usar data atual do cliente como padrão para as requisições de listagem da API
		if (localStorage.getItem('ECONRIO_CONFIG_APP_ANOBASE') == null)
			localStorage.setItem('ECONRIO_CONFIG_APP_ANOBASE', new Date().getFullYear());

		// cria cache das informações básicas da API utilizada nos vários componentes
		if (localStorage.getItem('ECONRIO_API_INFO_CACHE') == null) {
			const resposta = await fetch(API('localstorage')).catch(erros => {
				dispatch('routeEvent', { 
					tipo: TipoEvento.ESTA_CARREGANDO,
					valor: false
				});
	
				dispatch('routeEvent', {
					tipo: TipoEvento.ABRIR_MODAL,
					titulo: 'Erro',
					conteudo: 'Erro ao recuperar dados, tente novamente.'
				});
			});
			
			let informacoesAPI = await resposta.json();
			localStorage.setItem('ECONRIO_API_INFO_CACHE', JSON.stringify({...informacoesAPI}));
		}

		dispatch('routeEvent', { 
			tipo: TipoEvento.ESTA_CARREGANDO,
			valor: false
		});
	});
</script>

<TBox 
	subtitulo="Sistema de Cadastro e Avaliação de Projetos"
	iconePng={true}
	tamanhoIcone={128}
	largura={10}
	deslocamento={1}
	exibirBox={false}
	iconePngArquivo={icone}>
	
	<GradeCartoes itens={funcionalidadesInicio()} componente={CartaoQuadrado} />
</TBox>