<script>
	import { onMount } from 'svelte';
    import { parseJWT } from '../../utils/JWT';
	import Painel from './Painel.svelte';
    import TipoAcesso from '../../constantes/TipoAcesso';
    import { gerarQueryString, montarURN } from '../../utils/QueryString';

	const opcoes = [
		{ 
			titulo: "Voltar", 
			icone: "voltar",
			link: "#/painel",
			tiposAcesso: Object.values(TipoAcesso).toString()
		},
		{ 
			titulo: "Meu Cadastro", 
			icone: "usuario",
			tiposAcesso: Object.values(TipoAcesso).toString()
		},
		{ 
			titulo: "Listar", 
			icone: "listar",
			link: "#/usuario/listagem",
			tiposAcesso: `${TipoAcesso.ADMINISTRADOR},${TipoAcesso.BACKOFFICE}`
		},
		{ 
			titulo: "Buscar", 
			icone: "buscar",
			link: montarURN('busca/formulario', gerarQueryString({ tipo: 'USUARIO' })),
			tiposAcesso:`${TipoAcesso.ADMINISTRADOR},${TipoAcesso.BACKOFFICE}`
		}
	];
	
	onMount(async () => {
		const token = parseJWT(sessionStorage.getItem('ECONRIO_TOKEN'));
		
		if (token) {
			let meuCadastro = opcoes.filter(item => {
				return item.titulo == "Meu Cadastro";
			});
				
			meuCadastro = meuCadastro[0];
			meuCadastro.link = montarURN('usuario/visualizacao', gerarQueryString({ cpf: token.cpf }));
		}
	});	
</script>

<Painel 
	titulo="Painel de Usuários" 
	listaFuncionalidades={opcoes}>
</Painel>