<script>
	import { verificarSessionStorage } from './../../utils/Verificadores.js';
	import { createEventDispatcher, onMount } from 'svelte';
	import { replace } from 'svelte-spa-router';
    import TipoEvento from '../../constantes/TipoEvento';

	import TBox from '../templates/TBox.svelte';
    import { enviarDadosFormulario } from '../../utils/Requisicao.js';
	
	const dispatch = createEventDispatcher();
	
	// variáveis para trabalhar com os dados do formulário
	let titulo = ""; 
	let instituicao, alocacao, curso;

	let instituicoes = [];
	let areasConhecimento = [];
	let cursos = [];
	let envolvePesquisaAnimaisOuHumanos = 0;
	let numeroCAAE;
	let divNumeroCAAE;
	let informacosAPICache = null;
	
	let totalCaracteresTitulo = 0;
	$: totalCaracteresTitulo=titulo.length;

	onMount(async () => {
		dispatch('routeEvent', {
			tipo: TipoEvento.ESTA_CARREGANDO,
			valor: true,
			mensagem: 'Carregando dados ...',
		});
		
		if (!verificarSessionStorage('ECONRIO_TOKEN'))
			replace("/painel");

		informacosAPICache = JSON.parse(localStorage.getItem('ECONRIO_API_INFO_CACHE'));
		instituicoes = informacosAPICache.instituicoes;
		areasConhecimento = informacosAPICache.areas_conhecimento;
		cursos = informacosAPICache.cursos;
		divNumeroCAAE = document.getElementById('divNumeroCAAE');

		dispatch('routeEvent', { 
			tipo: TipoEvento.ESTA_CARREGANDO,
			valor: false
		});
	});

	async function submeterFormulario(event) {
		enviarDadosFormulario('POST', dispatch, replace, 'projetos', 
			JSON.stringify({
				titulo: titulo, 
				instituicao: instituicao,
				alocacao: alocacao,
				curso: curso,
				envolve_animais_humanos: envolvePesquisaAnimaisOuHumanos,
				numero_caae: numeroCAAE
			}), 
			'Projeto cadastrado com sucesso!', 
			'#/painel/projetos', 
			'#/painel');
	}

	const exibirEsconderNumeroCAEE = (evento) => {
		let escolha = parseInt(evento.currentTarget.value);

		if (escolha == 1)
			divNumeroCAAE.classList.remove('is-hidden');
		else
			divNumeroCAAE.classList.add('is-hidden');
	}
</script>

<TBox 
	titulo="Cadastro de Projeto" 
	subtitulo="Insira as informações sobre o projeto" 
	largura="12" 
	deslocamento="0" 
	icone="projeto">

	<div class="notification is-primary is-light">
		<p>Utilize o formulário abaixo para cadastrar as informações essenciais do seu projeto.</p>
		<p>O conteúdo do projeto e os participantes deverão ser cadastrados/indicados posteriormente.</p>
	</div>

	<br>

	<form method="post" on:submit|preventDefault={submeterFormulario}>
		<div class="columns">
			<div class="column">
				<small>Título do projeto (mín. 8 e máx. 100 caracteres ● Utilizados: {totalCaracteresTitulo}):</small>
				<div class="field">
					<input 
						bind:value={titulo}
						name="titulo" 
						type="text" 
						placeholder="Título do projeto" 
						class="input is-large is-primary" required>
				</div>
			</div>
		</div>

		<div class="columns">
			<div class="column">
				<small>Área de atuação do projeto:</small>
				<div class="select is-primary is-large is-fullwidth">
					<select bind:value={alocacao} name="unidade" required>
						<option value="">Selecione uma...</option>
						{#each Object.entries(areasConhecimento) as [chave, valor]}
						<option value="{chave}">{valor}</option>
						{/each}
					</select>
				</div>
			</div>
		</div>

		<div class="columns">
			<div class="column is-half">
				<small>Campus onde será desenvolvido o projeto:</small>
				<div class="select is-primary is-large is-fullwidth">
					<select bind:value={instituicao} name="unidade" required>
						<option value="">Unidade...</option>
						{#each Object.entries(instituicoes) as [chave, valor]}
						<option value="{chave}">{valor}</option>
						{/each}
					</select>
				</div>
			</div>

			<div class="column is-half">
				<small>Curso:</small>
				<div class="select is-primary is-large is-fullwidth">
					<select bind:value={curso} name="unidade" required>
						<option value="">Selecione...</option>
						{#each Object.entries(cursos) as [chave, valor]}
						<option value="{chave}">{valor}</option>
						{/each}
					</select>
				</div>
			</div>
		</div>
		
		<div class="columns">
			<div class="column">
				<small>A pesquisa envolve a participação direta ou indireta de seres humanos ou experimento com animais?</small>
				<div class="control">
					<label class="radio">
						<input type="radio" bind:group={envolvePesquisaAnimaisOuHumanos} value={1} on:change={exibirEsconderNumeroCAEE}>
						Sim
					</label>
					<label class="radio">
						<input type="radio" bind:group={envolvePesquisaAnimaisOuHumanos} value={0} on:change={exibirEsconderNumeroCAEE}>
						Não
					</label>
				</div>
			</div>
		</div>

		<div class="columns is-hidden" id="divNumeroCAAE">
			<div class="column is-half">
				<small>Insira o número do Certificado de Apresentação para Apreciação Ética (CAAE), emitido por um Comitê de Ética em Pesquisa (CEP) ou comprovante de submissão do projeto a uma Comissão de Ética no Uso de Animais (CEUA) ou número de protocolo de aprovação do projeto.</small>
			</div>
			<div class="column">
				<input 
					bind:value={numeroCAAE}
					type="text"
					placeholder="Número CAAE" 
					class="input is-large is-primary">
			</div>
		</div>

		<div class="columns">
			<div class="column">
				<p class="control">
					<a href="#/" class="button is-black is-medium is-outlined is-fullwidth">Início</a>
				</p>
			</div>
			<div class="column">
				<p class="control">
					<a href="#/painel" class="button is-black is-medium is-outlined is-fullwidth">Painel</a>
				</p>
			</div>
			<div class="column is-8">
				<p class="control">
					<button 
						type="submit" 
						class="button is-primary is-medium is-fullwidth">
						Cadastrar
					</button>
				</p>
			</div>
		</div>
	</form>
</TBox>