export const verificarLocalStorage = (chave) => localStorage.getItem(chave) != null ? true : false;
export const verificarSessionStorage = (chave) => sessionStorage.getItem(chave) != null ? true : false;
export const verificarSeExisteCacheAvaliacoes = (pagina) => localStorage.getItem(`ECONRIO_LISTAGEM_AVALIACOES_CACHE_PG${pagina}`) != null ? true : false;
export const verificarSeExisteCacheProjetos = (pagina) =>localStorage.getItem(`ECONRIO_LISTAGEM_PROJETOS_CACHE_PG${pagina}`) != null ? true : false;
export const verificarSeExisteCacheUsuarios = (pagina) => localStorage.getItem(`ECONRIO_LISTAGEM_USUARIOS_CACHE_PG${pagina}`) != null ? true : false;
export const verificarSeExisteCacheChaveLS = (pagina, chaveLS) => localStorage.getItem(chaveLS + pagina) != null ? true : false;
