<script>
	import { onMount, createEventDispatcher } from 'svelte';
	import { replace } from 'svelte-spa-router';
	import { sTituloPaginaAtual } from '../../stores';
	import { verificarSessionStorage } from '../../utils/Verificadores';
    import { parseJWT } from '../../utils/JWT';
	import { limparCacheAplicacao } from '../../utils/Storage';
	
	import PainelBox from '../templates/PainelBox.svelte';
	import GradeCartoes from '../../componentes/GradeCartoes.svelte';
	import CartaoQuadrado from '../../componentes/cartoes/CartaoQuadrado.svelte';
    import TipoEvento from '../../constantes/TipoEvento';

	export let listaFuncionalidades = [];
	export let titulo = "Painel";
	
	let filtroFuncionalidades = [];
	const dispatch = createEventDispatcher();
	
	onMount(async () => {
		$sTituloPaginaAtual = titulo;

		if (verificarSessionStorage('ECONRIO_TOKEN') == false) {			
			
			limparCacheAplicacao(true, false, false);

			dispatch('routeEvent', {
				tipo: TipoEvento.ABRIR_MODAL,
				titulo: 'Aviso',
				conteudo: 'Necessário realizar login.'
			});

			setTimeout( () => replace('/login'), 1000);
		}

		const token = parseJWT(sessionStorage.getItem('ECONRIO_TOKEN'));
		
		// filtra as opções do menu de acordo com o tipoAcessoEmUso do usuário
		filtroFuncionalidades = listaFuncionalidades.filter( item => {
			return item.tiposAcesso.split(',').find(valor => valor == token.tipo_em_uso);
		});
	});
</script>

<PainelBox 
	largura="12" 
	deslocamento="0">
	<GradeCartoes itens={filtroFuncionalidades} componente={CartaoQuadrado} />
</PainelBox>
