/**
 * formato url = /x/y?q=timestampCodificado&PARAMETRO_OPCIONAL_CODIFICADO...
 * a querystring timestampCodificado será utilizada para verificação da navegação do usuário,
 * onde deverá ser válida apenas dentro de um minuto entre um clique e outro, evitando com
 * isso favoritar URL, forçando sempre o usuário a acessar a raíz (novos dados são consultados na API etc).
 * Usar & para separar os demais parâmetros opcionais (todos devem ser codificados com função própria em utils.js)
 */

