/**
 * algumas chaves não serão removidas pois são dados estáticos 
 * do sistema (pouca mudança na API) ou são de personalização do usuário (tema etc)
 * @return void
 */
 export const limparCacheAplicacao = (limparToken, limparCacheUsuarios, limparCacheProjetos) => {
	if (limparToken === true)
		sessionStorage.removeItem('ECONRIO_TOKEN');
	
	// limpa cache dos usuarios
	if (limparCacheUsuarios === true) {
		Object.keys(localStorage)
			.filter( nomeChave => nomeChave.includes('ECONRIO_LISTAGEM_USUARIOS_CACHE_PG'))
			.forEach(item => localStorage.removeItem(item));
	}

	// limpa cache dos projetos
	if (limparCacheProjetos === true) {
		Object.keys(localStorage)
			.filter( nomeChave => nomeChave.includes('ECONRIO_LISTAGEM_PROJETOS_CACHE_PG'))
			.forEach(item => localStorage.removeItem(item));
	}
}

export const limparCacheTotal = () => {
	Object.keys(localStorage)
		.filter(nomeChave => nomeChave.includes('ECONRIO_'))
		.forEach(item => localStorage.removeItem(item));

	Object.keys(sessionStorage)
		.filter(nomeChave => nomeChave.includes('ECONRIO_'))
		.forEach(item => sessionStorage.removeItem(item));
}