<script>
	import { formatarCPF } from './../../utils/CPF.js';
	import { createEventDispatcher, onMount } from 'svelte';
	import { replace } from 'svelte-spa-router';
	import { sCaptcha } from '../../stores';
	import TBox from '../templates/TBox.svelte';
	import Captcha from '../../componentes/Captcha.svelte';
    import { verificarSessionStorage } from '../../utils/Verificadores';
    import { API, cabecalhoRequisicao, enviarDadosFormulario } from '../../utils/Requisicao';
    import TipoEvento from '../../constantes/TipoEvento';
    import TextoFeedback from '../../constantes/TextoFeedback';
    import { gerarStringAleatoria } from '../../utils/Geradores';
    import StatusHttp from '../../constantes/StatusHttp';
	const dispatch = createEventDispatcher();
	
	// variáveis para trabalhar com os dados do formulário
	let senha, captchaUsuario;
	let tituloAviso = "Erro";
	let feedbackUsuario = [];
	let meusProjetos = [];
	let participanteRetornado, papelParticipante;
	let participanteSelecionadoHtml;
	let codigoProjeto;
	let cpfParticipanteInformado;
	let botaoEnvioDesabilitado = true;
	let totalProjetos = 0;
	let instituicoes = [];
	let informacosAPICache = null;
	let inputDataPrevisaoConclusao;
	let divDescricaoDescontoOuBolsa;
	let divDadosComplementares;

	// variáveis para os dados complementares (dados do aluno)
	let nome, email, matricula, cra, campus, previsaoConclusaoCurso, descricaoDescontoOuBolsa;
	let jaParticipaPIC = 0;
	let possuiDescontoOuBolsa = 0;
	let parmanecerComoVoluntario = 1;

	onMount(async () => {
		if (verificarSessionStorage('ECONRIO_TOKEN')) {
			informacosAPICache = JSON.parse(localStorage.getItem('ECONRIO_API_INFO_CACHE'));
			instituicoes = informacosAPICache.instituicoes;

			await carregarProjetosAPI();
		} else replace("/login");
		if (totalProjetos > 0) {
			inputDataPrevisaoConclusao = document.getElementById('previsaoConclusaoCurso');
			const hoje = new Date().toISOString().split("T")[0];
			const daqui10Anos = new Date().setFullYear(new Date().getFullYear() + 10);

			inputDataPrevisaoConclusao.min = hoje;
			inputDataPrevisaoConclusao.max = new Date(daqui10Anos).toISOString().split("T")[0];

			divDescricaoDescontoOuBolsa = document.getElementById('divDescricaoDescontoOuBolsa');
			divDadosComplementares = document.getElementById('dadosComplementares');
		}
			
		else {
			dispatch('routeEvent', {
				tipo: TipoEvento.ABRIR_MODAL,
				titulo: 'Ação inválida',
				conteudo: 'Não é possível executar indicação. Nenhum projeto cadastrado.'
			});
			replace("/painel/projetos");
		}
	});
	
	const carregarProjetosAPI = async () => {
		dispatch('routeEvent', {
			tipo: TipoEvento.ESTA_CARREGANDO,
			valor: true,
			mensagem: 'Carregando projetos ...',
		});
		const resposta = await fetch(API('projetos'), {
			method: 'GET',
			headers: cabecalhoRequisicao()
		}).catch(erros => {
			dispatch('routeEvent', {
				tipo: TipoEvento.ABRIR_MODAL,
				titulo: 'Erro',
				conteudo: TextoFeedback.ERRO_REQUISICAO
			});
			replace("/login");
		});
		
		const json = await resposta.json();

		if (resposta.status == StatusHttp.OK) {
			meusProjetos = [...json.dados.data];
			totalProjetos = meusProjetos.length;
		} else {
			dispatch('routeEvent', {
				tipo: TipoEvento.ABRIR_MODAL,
				titulo: 'Erro',
				conteudo: json?.mensagem
			});
			
			replace("/login");
		}
		dispatch('routeEvent', { 
			tipo: TipoEvento.ESTA_CARREGANDO,
			valor: false
		});

		return totalProjetos;
	}
	 
	async function submeterFormulario(event) {
		if (captchaUsuario != $sCaptcha) {
			dispatch('routeEvent', { 
				tipo: TipoEvento.ESTA_CARREGANDO,
				valor: false
			});
			
			dispatch('routeEvent', {
				tipo: TipoEvento.ABRIR_MODAL,
				titulo: 'Captcha Inválido',
				conteudo: 'O código informado é inválido, tente novamente.'
			});
			
			// atualiza o store
			sCaptcha.update(novaString => gerarStringAleatoria());
		} else {
			// faz a chamada a API no endpoint atualizar, envia os dados e aguarda a resposta
			enviarDadosFormulario('POST', dispatch, replace, 'participantes' , JSON.stringify({
				senha: senha,
				codigoProjeto: codigoProjeto,
				papel: papelParticipante,
				
				dadosComplementares: {
					nome: nome, 
					cpf: cpfParticipanteInformado,
					email: email, 
					matricula: matricula,
					cra: cra,
					campus: campus, 
					previsao_conclusao_curso: previsaoConclusaoCurso,
					nome_bolsa_desconto: descricaoDescontoOuBolsa,
					participa_pic: jaParticipaPIC,
					possui_bolsa_desconto: possuiDescontoOuBolsa,
					aceita_voluntariado: parmanecerComoVoluntario
				}
			}), 'Indicação realizada com sucesso!', 
				'#/painel', 
				null, false
			);
		}
	}
	
	async function buscarParticipantePorCPF(event) {
		dispatch('routeEvent', {
			tipo: TipoEvento.ESTA_CARREGANDO,
			valor: true,
			mensagem: 'Buscando ...',
		});
			
		// faz a chamada a API no endpoint cadastrar, envia os dados e aguarda a resposta
		const resposta = await fetch(API('consultas/usuarios'), {
			method: 'POST',
			headers: cabecalhoRequisicao(),
			body: JSON.stringify({
				cpf: cpfParticipanteInformado,
			})
		}).catch(erros => {
			feedbackUsuario.push(TextoFeedback.ERRO_REQUISICAO);
			
			dispatch('routeEvent', { 
				tipo: TipoEvento.ESTA_CARREGANDO,
				valor: false
			});
			
			dispatch('routeEvent', {
				tipo: TipoEvento.ABRIR_MODAL,
				titulo: tituloAviso,
				conteudo: feedbackUsuario[0]
			});
		});
		// pega o json do retorno da resposta
		const json = await resposta.json();

		if (resposta.status == StatusHttp.OK) {
			participanteRetornado = json.dados;
			participanteSelecionadoHtml = `
				<p>Participante: <strong>${participanteRetornado.nome}</strong></p>
				<p>E-mail: <strong>${participanteRetornado.email}</strong></p>
				<p>CPF: <strong>${formatarCPF(participanteRetornado.cpf)}</strong></p>
			`;
			botaoEnvioDesabilitado = false;
			divDadosComplementares.classList.remove('is-hidden');
			nome = participanteRetornado?.nome;
			email = participanteRetornado?.email;
		} else if (resposta.status == StatusHttp.HTTP_NOT_FOUND) {
			tituloAviso = "Aviso!"
			feedbackUsuario.push('Participante não encontrado com o CPF informado. Preencha os dados do participante para continuar.');
			botaoEnvioDesabilitado = false;
			divDadosComplementares.classList.remove('is-hidden');
		} else {
			feedbackUsuario.push(json?.mensagem ?? 'Erro desconhecido, se persistir, favor entrar em contato.');
		}
		dispatch('routeEvent', { 
			tipo: TipoEvento.ESTA_CARREGANDO,
			valor: false
		});
		// exibe feedback pro usuário se existir algo pra exibir
		if (feedbackUsuario[0]) {
			dispatch('routeEvent', {
				tipo: TipoEvento.ABRIR_MODAL,
				titulo: tituloAviso,
				conteudo: feedbackUsuario[0]
			});
		}
		// limpa as mensagens
		feedbackUsuario = [];
	}

	const exibirEsconderDescricaoDescontoOuBolsa = (evento) => {
		let escolha = parseInt(evento.currentTarget.value);

		if (escolha == 1)
			divDescricaoDescontoOuBolsa.classList.remove('is-hidden');
		else
			divDescricaoDescontoOuBolsa.classList.add('is-hidden');
	}
</script>
<TBox 
	titulo="Indicação Participante" 
	subtitulo="Utilize o formulário abaixo para indicar um(a) participante" 
	largura="8" 
	deslocamento="2" 
	correcaoY="-13px"
	icone="indicar">
	
	<form method="post" on:submit|preventDefault={submeterFormulario} autocomplete="off">
		<p>Escolha um de seus projetos para indicar:</p>
		
		<div class="columns">
			<div class="column">
				<div class="select is-primary is-large is-fullwidth">
					<select bind:value={codigoProjeto} name="projeto" required>
						<option value="">Selecione um projeto...</option>
						{#each meusProjetos as projeto}
						<option value="{projeto.codigo}">{projeto.titulo}</option>
						{/each}
					</select>
				</div>
			</div>
		</div>
		<p>Digite o CPF do participante que irá participar do projeto:</p>
		<div class="columns">
			<div class="column">
				<div class="field has-addons">
					<div class="control is-expanded">
						<input 
							bind:value={cpfParticipanteInformado}
							name="cpf_participante"
							type="tel" 
							pattern="[0-9]*"
							maxlength="11"
							placeholder="Somente números..." 
							class="input is-large is-primary" required>
					</div>
					<div class="control">
						<button class="button is-info is-large" on:click|preventDefault={buscarParticipantePorCPF}>Buscar</button>
					</div>
				</div>
			</div>
		</div>
		
		<p>Selecione o papel que o(a) indicado(a) exercerá no projeto:</p>
		<div class="columns">
			<div class="column">
				<div class="select is-primary is-large is-fullwidth">
					<select bind:value={papelParticipante} name="papel" required>
						<option value="">Escolha ...</option>
						<option value="BOLSISTA">Bolsista</option>
						<option value="VOLUNTARIO">Voluntário(a)</option>
					</select>
				</div>
			</div>
		</div>

		<div id="dadosComplementares" class="is-hidden">
			<hr>
			<p class="has-text-info">Utilize os campos abaixo para preencher informações do(a) aluno(a) que irá compor a equipe do projeto.</p>

			<div class="columns">
				<div class="column is-half">
					<small class="has-text-primary">Nome completo</small>
					<input 
						bind:value={nome}
						type="text"
						placeholder="Nome" 
						class="input is-large is-primary" required>
				</div>
				<div class="column is-half">
					<small class="has-text-primary">E-mail</small>
					<input 
						bind:value={email}
						type="email"
						placeholder="E-mail" 
						class="input is-large is-primary" required>
				</div>
			</div>

			<div class="columns">
				<div class="column is-half">
					<small class="has-text-primary">Matrícula</small>
					<input 
						bind:value={matricula}
						type="text"
						placeholder="Matrícula" 
						class="input is-large is-primary" required>
				</div>
				<div class="column is-half">
					<small class="has-text-primary">Coeficiente de rendimento acumulado (CRA)</small>
					<input 
						bind:value={cra}
						type="text"
						placeholder="CRA" 
						class="input is-large is-primary" required>
				</div>
			</div>

			<div class="columns">
				<div class="column is-half">
					<small class="has-text-primary">Campus:</small>
					<div class="select is-primary is-large is-fullwidth">
						<select bind:value={campus} name="unidade" required>
							<option value="">Selecione...</option>
							{#each Object.entries(instituicoes) as [chave, valor]}
							<option value="{chave}">{valor}</option>
							{/each}
						</select>
					</div>
				</div>
				<div class="column is-half">
					<small class="has-text-primary">Previsão de conclusão do curso:</small>
					<input type="date" 
						bind:value={previsaoConclusaoCurso}
						id="previsaoConclusaoCurso" 
						max="2024-12-31" class="input is-large is-primary" required>
				</div>
			</div>

			<div class="columns">
				<div class="column">
					<small class="has-text-primary">Já participa oficialmente de Programa de Iniciação Cientifica?</small>
					<div class="control">
						<label class="radio">
							<input type="radio" bind:group={jaParticipaPIC} value={1}>
							Sim
						</label>
						<label class="radio">
							<input type="radio" bind:group={jaParticipaPIC} value={0}>
							Não
						</label>
					</div>
				</div>
			</div>

			<div class="columns">
				<div class="column">
					<small class="has-text-primary">Possui outro tipo de bolsa ou desconto?</small>
					<div class="control">
						<label class="radio">
							<input type="radio" bind:group={possuiDescontoOuBolsa} value={1} on:change={exibirEsconderDescricaoDescontoOuBolsa}>
							Sim
						</label>
						<label class="radio">
							<input type="radio" bind:group={possuiDescontoOuBolsa} value={0} on:change={exibirEsconderDescricaoDescontoOuBolsa}>
							Não
						</label>
					</div>
				</div>
			</div>

			<div class="columns is-hidden" id="divDescricaoDescontoOuBolsa">
				<div class="column">
					<small class="has-text-primary">Qual?</small>
					<input 
						bind:value={descricaoDescontoOuBolsa}
						type="text"
						placeholder="Descreva aqui ..." 
						class="input is-large is-primary">
				</div>
			</div>

			<div class="columns">
				<div class="column">
					<small class="has-text-primary">Caso o projeto seja aprovado e ocorra a impossibilidade de usufruir a bolsa de Iniciação Científica, aluno(a) deseja permanecer no projeto como voluntário?</small>
					<div class="control">
						<label class="radio">
							<input type="radio" bind:group={parmanecerComoVoluntario} value={1}>
							Sim
						</label>
						<label class="radio">
							<input type="radio" bind:group={parmanecerComoVoluntario} value={0}>
							Não
						</label>
					</div>
				</div>
			</div>
			<hr>
		</div>

		<div class="columns">
			<div class="column">
				<div class="field">
					<input 
						bind:value={senha}
						name="senha" 
						type="password" 
						placeholder="Informe sua senha" 
						class="input is-large is-primary" required>
				</div>
			</div>
		</div>
		<div class="columns is-mobile">
			<div class="column">
				<Captcha />
			</div>
			<div class="column">
				<input 
					name="captcha"
					bind:value={captchaUsuario}
					type="text"
					maxlength="8" 
					placeholder="Digite o código aqui" 
					class="input is-large is-primary" required>
			</div>
		</div>
		<div class="columns">
			<div class="column">
				<p class="control">
					<a href="#/painel" class="button is-black is-medium is-outlined is-fullwidth">Voltar</a>
				</p>
			</div>
			<div class="column is-8">
				<p class="control">
					<button 
						type="submit" 
						disabled={botaoEnvioDesabilitado}
						class="button is-success is-medium is-fullwidth">
						Indicar
					</button>
				</p>
			</div>
		</div>
	</form>
</TBox>