<script>
	import { createEventDispatcher, onMount } from 'svelte';
	import { replace, querystring } from 'svelte-spa-router';
    import { recuperarValorParametroQS } from '../../utils/QueryString';
    import { limparCacheTotal } from '../../utils/Storage';
    import TipoEvento from '../../constantes/TipoEvento';
	
	const dispatch = createEventDispatcher();
	
	onMount(async () => {
		let parametrosURL = new URLSearchParams($querystring);
		let mensagem = recuperarValorParametroQS(parametrosURL.get('q'), 'mensagem');
		
		dispatch('routeEvent', {
			tipo: TipoEvento.ESTA_CARREGANDO,
			valor: true,
			mensagem: mensagem,
		});

		limparCacheTotal();
		setTimeout( () => replace('#/'), 2000);
	});
</script>
