import TextoFeedback from "../constantes/TextoFeedback";

const codificarQueryString = (queryString) => window.btoa(encodeURIComponent(queryString));

export const decodificarQueryString = (queryStringCodificada) => decodeURIComponent(window.atob(queryStringCodificada));

export const recuperarValorParametroQS = (queryString, parametro) => {
	let q = decodificarQueryString(queryString);
	let p = new URLSearchParams(q).get(parametro);
	// if (!p) throw new Error(TextoFeedback.PARAMETRO_INVALIDO);

	return new URLSearchParams(q).get(parametro);
}

/**
 * Executa validação de uma querystring para verificar se a mesma foi gerada a mais de x minutos
 * @param string queryString 
 * @param number valor inteiro dos minutos que se deseja considerar a vida útil de uma querystring
 * @throws Error
 * @returns void
 */
 export const verificarTimestampQueryString = (queryString, minutosVidaUtil = 5) => {
	let parametrosURL = new URLSearchParams(decodificarQueryString(queryString));
	let timestampQS = new Date(parseInt(parametrosURL.get('_ts')));

	// faz verificação do tempo atual com cópia de tempo adicionado de {minutosVidaUtil} minutos do timestamp da querystring
	let copiaTimestampAcrescidoXMin = new Date(timestampQS.getTime()).setMinutes(timestampQS.getMinutes() + minutosVidaUtil);
	
	if (new Date().getTime() >= copiaTimestampAcrescidoXMin)
		throw new Error(TextoFeedback.LINK_EXPIRADO);
};

/**
 * Gera uma querystring a partir de um objeto fornecido
 * @param Object {chave: valor} objetoParamentros 
 * @returns string
 */
 export const gerarQueryString = (objetoParamentros) => {
	// gera o primeiro parametro dinamico que é o carimbo de tempo do momento da geração da querystring
	let timestampQS = new Date().getTime();

	// faz merge do timestamp no objeto passado com os parametros
	let objetoParamentrosComTimestamp = Object.assign({}, {
		_ts: timestampQS
	}, objetoParamentros);

	// retorna querystring codificada
	let qs = Object.keys(objetoParamentrosComTimestamp).map(key => key + '=' + objetoParamentrosComTimestamp[key]).join('&');
	return codificarQueryString(qs);
}

export const montarURN = (rota, queryStringCodificada) => `#/${rota}?q=${queryStringCodificada}`;

